import Vue from "vue";
import App from "./App.vue";
import { CordovaModel, PreviewPlayer, Export } from "jsugc-websdk";
import MuseUI from "muse-ui";
import "muse-ui/dist/muse-ui.css";
import "@/assets/icon/material-icons.css";
import "muse-ui-message/dist/muse-ui-message.css";
import Message from "muse-ui-message";

import Snotify from "vue-snotify";
import "vue-snotify/styles/material.css";

Vue.use(MuseUI);
Vue.use(Message);
Vue.use(Snotify, {
    toast: {
        position: "centerTop",
    },
});

Vue.component("PreviewPlayer", PreviewPlayer);
Vue.component("Export", Export);

Vue.config.productionTip = false;
Vue.config.performance = true;

Vue.prototype.isCordova = CordovaModel.isCordova;
Vue.prototype.isIOS = CordovaModel.isIOS;
Vue.prototype.isAndroid = CordovaModel.isAndroid;
Vue.prototype.isMobile = () => {
    return CordovaModel.isIOS() || CordovaModel.isAndroid();
};
Vue.prototype.isPortrait = CordovaModel.isPortrait;

(async () => {
    await CordovaModel.init();

    new Vue({
        render: (h) => h(App),
    }).$mount("#app");
})();
