<template>
    <div v-if="!output.desSrc" class="exporter-main">
        <div class="main-title">
            <mu-button @click="cancel" large icon>
                <mu-icon color="white" size="36" value="close" />
            </mu-button>
        </div>
        <div class="title">
            
            <span></span>
        </div>
        <Export ref="export" @error="error" @success="success" class="content">
            <template #default="param">
                <img :src="thumbUrl" v-if="thumbUrl" :style="posterStyles" />
                <div class="progress">
                    <mu-circular-progress
                        class="progress-item progress-slave"
                        mode="determinate"
                        color="rgba(255,255,255,0.3"
                        :value="100"
                        :stroke-width="10"
                        :size="105"
                    ></mu-circular-progress>
                    <mu-circular-progress
                        class="progress-item progress-master"
                        mode="determinate"
                        color="red"
                        :value="param.progress"
                        :stroke-width="5"
                        :size="100"
                    ></mu-circular-progress>
                    <div class="progress-item">{{param.progress}}%</div>
                </div>
            </template>
        </Export>
        <div class="bottom"></div>
    </div>
    <div v-else class="exporter-main">
        <div class="main-title">
            <mu-button @click="cancel" large icon>
                <mu-icon color="white" size="36" value="close" />
            </mu-button>
            <mu-button @click="done" large icon class="btn-done">
                <mu-icon color="white" size="36" value="done" />
            </mu-button>
        </div>
        <div class="title">
            <mu-icon value="check" />
            <span></span>
        </div>
        <div class="content">
            <!-- <video :src="output.desSrc" controls controlslist="nodownload" /> -->

            <VueVideoPlayer
                class="video-player-box vjs-custom-skin"
                ref="videoPlayer"
                :options="playerOptions"
                :playsinline="true"
            ></VueVideoPlayer>
        </div>
        <div class="bottom"></div>
    </div>
</template>

<style lang="less" scoped>
