<style>
.vjs-custom-skin > .video-js {
    height: 100%;
}
</style>

<script>
import { Editlist, CordovaModel, Log } from "jsugc-websdk";
import { videoPlayer, videojs } from "vue-video-player";
import "video.js/dist/video-js.css";
import "vue-video-player/src/custom-theme.css";

//  seekbar 
const SeekBar = videojs.getComponent("SeekBar");
SeekBar.prototype.getPercent = function getPercent() {
    // Allows for smooth scrubbing, when player can't keep up.
    // const time = (this.player_.scrubbing()) ?
    //   this.player_.getCache().currentTime :
    //   this.player_.currentTime()
    const time = this.player_.currentTime();
    const percent = time / this.player_.duration();
    return percent >= 1 ? 1 : percent;
};

SeekBar.prototype.handleMouseMove = function handleMouseMove(event) {
    let newTime = this.calculateDistance(event) * this.player_.duration();
    if (newTime === this.player_.duration()) {
        newTime = newTime - 0.1;
    }
    this.player_.currentTime(newTime);
    this.update();
};

export default {
    components: { VueVideoPlayer: videoPlayer },
    data() {
        return {
            thumbUrl: "",
            posterUrl: "",
            posterRotate: null,
            isTempPoster: false,
            exportProgress: 0,
            output: {
                desSrc: "",
            },
            errorMessage: "",

            playerOptions: {
                // nativeControlsForTouch: true,
                language: "cn",
                sources: [
                    {
                        type: "video/mp4",
                        src: "",
                    },
                ],
                poster: "",
            },
        };
    },
    computed: {
        posterStyles() {
            // 
            if (this.posterRotate && !this.isTempPoster) {
                return this.posterRotate
                    ? "transform: rotate(" + this.posterRotate + "deg)"
                    : "";
            }
            return {};
        },
    },
    methods: {
        async done() {
            let config = await CordovaModel.getConfig();
            let result = {
                statusCode: CordovaModel.StatusCode.Ok,
                message: this.errorMessage || "",
                output: config.output,
                output_thumb: config.output_thumb,
            };
            Log.uilog("/exporter", `done. ${JSON.stringify(result)}`);
            CordovaModel.finish(result);
        },
        async cancel() {
            Log.uilog("/exporter", `cancel`);

            if (this.$refs.export) await this.$refs.export.cancel();
            this.$emit("close");
        },
        error(result) {
            this.errorMessage = result.message;
            this.$snotify.error("" + this.errorMessage);
        },
        async success(desSrc) {
            this.output.desSrc = this.playerOptions.sources[0].src = desSrc;
            this.playerOptions.poster = this.thumbUrl;
        },
    },
    async mounted() {
        this.posterUrl = Editlist.editlist.poster.url;
        this.posterRotate = Editlist.editlist.poster.rotate;

        try {
            this.thumbUrl = await CordovaModel.getCdvUrl(this.posterUrl);
        } catch (error) {
            this.posterUrl = null;
            // window.console.error("fail get poster", error, this.posterUrl);
        }

        document.addEventListener("backbutton", this.cancel, false);

        Log.uilog("/exporter", `begin`);
        this.$refs.export.begin();
    },
    async beforeDestroy() {
        document.removeEventListener("backbutton", this.cancel, false);
    },
};
</script>
