<template>
    <div class="box">
        <div class="border">
            <mu-icon v-if="loading" value="refresh" class="loading" size="30"></mu-icon>
            <canvas class="main" ref="canvas" width="1024" height="500" :style="canvasStyles"></canvas>
        </div>
    </div>
</template>
<style scoped>
.box {
    position: absolute;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: flex-end;
    display: flex;
    background: rgba(0, 0, 0, 1);
}
.border {
    display: inline-block;
    background: rgba(0, 0, 0, 1);
    /* height:30px; */
    border: 1px solid #555;
    border-radius: 5px;
    padding: 10px 10px;
    padding-bottom: 10px;
    box-sizing: content-box;
    position: relative;
}
.main {
    object-fit: contain;
    display: inline-block;
    max-width: 100%;
}

.loading {
    position: absolute;
    left: 50%;
    top: 50%;
    animation: loading 0.3s infinite linear;
    color: #fff;
    text-shadow: 0 0 30px #000;
    opacity: 0.9;
    transform-origin: 0 0;
}

@keyframes loading {
    from {
        transform: rotate(0deg) translate(-50%, -50%);
    }
    to {
        transform: rotate(360deg) translate(-50%, -50%);
    }
}
</style>
<script>
import colorAlpha from "color-alpha";
import Const from "@/const";
// import {UGCResourceManager as RManager} from "jsugc-websdk";
import throttle from "lodash.throttle";
import CanvasTextWrapper from "@/model/CanvasTextWrapper";

let DefaultSize = 1000;
let DefaultFont = "";

export default {
    props: [
        "text",
        "font",
        "fontUrl",
        "align",
        "color",
        "transparent",
        "borderColor",
        "borderSize",
        "shadowColor",
        "shadowLevel",
        "backgroundColor",
        "backgroundTransparent",
        "templateName",
        "templateBgUrl",
        "endX",
        "endY",
        "startX",
        "startY",
        "wordCount"
    ],
    watch: {
        text: "delayRefresh",
        font: "delayRefresh",
        fontUrl: "delayRefresh",
        align: "delayRefresh",
        color: "delayRefresh",
        transparent: "delayRefresh",
        borderColor: "delayRefresh",
        borderSize: "delayRefresh",
        shadowColor: "delayRefresh",
        shadowLevel: "delayRefresh",
        backgroundColor: "delayRefresh",
        backgroundTransparent: "delayRefresh",

        templateBgUrl: "delayRefresh",
        startX: "delayRefresh",
        startY: "delayRefresh",
        endX: "delayRefresh",
        endY: "delayRefresh",
        wordCount: "delayRefresh"
    },
    data() {
        return {
            loading: false,
            delayRefresh: throttle(
                () => {
                    if (this.$refs.canvas) this.refresh();
                },
                200,
                {
                    leading: false,
                    trailing: true
                }
            ),
            textSize: DefaultSize,
            textAreaWidth: 0,
            textAreaHeight: 0,
            textLines: []
        };
    },
    computed: {
        canvasStyles() {
            return {
                "max-height": this.templateBgUrl
                    ? "80px"
                    : this.isHorizontal
                    ? "50px"
                    : "120px"
            };
        }
    },
    methods: {
        async refresh() {
            // var a = Date.now();
            let can = this.$refs.canvas;
            let ctx = this.canvasCtx;

            // 
            await this._loadFont();

            if (this.templateBgUrl) {
                let bgImg = await new Promise((o, x) => {
                    const img = new Image();
                    img.onload = () => o(img);
                    img.onerror = x;
                    img.crossOrigin = "anonymous";
                    img.src = this.templateBgUrl;
                });

                can.width = bgImg.naturalWidth;
                can.height = bgImg.naturalHeight;
                ctx.clearRect(0, 0, can.width, can.height);

                // 
                if (this.backgroundColor) {
                    ctx.fillStyle = colorAlpha(
                        this.backgroundColor,
                        1 - this.backgroundTransparent
                    );
                    ctx.fillRect(0, 0, can.width, can.height);
                }

                // 
                if (bgImg && bgImg.width) {
                    ctx.drawImage(bgImg, 0, 0, can.width, can.height);
                }

                // 
                if (this.text.length) {
                    let rect = {
                        x: bgImg.naturalWidth * this.startX,
                        y: bgImg.naturalHeight * this.startY,
                        width: bgImg.naturalWidth * (this.endX - this.startX),
                        height: bgImg.naturalHeight * (this.endY - this.startY)
                    };
                    let textCanvas = this.drawText(this.text, rect, {
                        textAlign:
                            this.align == Const.Overlay.Align.Left
                                ? "left"
                                : this.align == Const.Overlay.Align.Right
                                ? "right"
                                : this.align == Const.Overlay.Align.Top
                                ? "top"
                                : "center"
                    });
                    ctx.drawImage(textCanvas, rect.x, rect.y);
                }
            } else {
                // 
                // size=1=/
                let lines = this.text.split("\n");
                let maxLen = lines.reduce(
                    (p, n) =>
                        Math.max(p, n.replace(/[^\x0-\xff]/g, "xx").length / 2),
                    0
                );

                let text = this.text;
                if (this.align == Const.Overlay.Align.Top) {
                    can.width = Math.ceil(
                        (DefaultSize / maxLen) * lines.length
                    );
                    can.height = DefaultSize;
                } else {
                    can.width = DefaultSize;
                    can.height = Math.ceil(
                        (DefaultSize / maxLen) * lines.length
                    );
                }

                // 
                if (this.backgroundColor) {
                    ctx.fillStyle = colorAlpha(
                        this.backgroundColor,
                        1 - this.backgroundTransparent
                    );
                    ctx.fillRect(0, 0, can.width, can.height);
                }

                if (this.text.length) {
                    let rect = {
                        x: 0,
                        y: 0,
                        width: can.width,
                        height: can.height
                    };

                    let textCanvas = this.drawText(text, rect, {
                        lineBreak: "word",
                        textAlign:
                            this.align == Const.Overlay.Align.Left
                                ? "left"
                                : this.align == Const.Overlay.Align.Right
                                ? "right"
                                : this.align == Const.Overlay.Align.Top
                                ? "top"
                                : "center"
                    });
                    ctx.drawImage(textCanvas, rect.x, rect.y);
                }
            }
            // window.console.log("TextRender time: ", Date.now() - a);
        },
        //  text  rect  align 
        drawText(text, rect, opt) {
            let canvas = document.createElement("canvas");
            let context = canvas.getContext("2d");
            canvas.width = rect.width;
            canvas.height = rect.height;
            context.shadowColor = this.shadowColor;
            context.strokeStyle = this.borderColor;
            context.fillStyle = colorAlpha(this.color, 1 - this.transparent);

            if (text)
                CanvasTextWrapper(canvas, text, {
                    textAlign: "center",
                    verticalAlign: "middle",
                    sizeToFill: true,
                    strokeText: true,
                    renderHDPI: false,
                    font: "10px " + (this.font || DefaultFont),
                    lineWidth: (0.05 * this.borderSize) / 5,
                    shadowBlur: (0.5 * this.shadowLevel) / 10,
                    paddingX: Math.min(rect.width, rect.height) / 20,
                    paddingY: Math.min(rect.width, rect.height) / 20,
                    ...opt
                });

            return canvas;
        },
        async _loadFont() {
            if (this.fontUrl) {
                if (document.fonts) {
                    // ios 10 & android 5
                    let font = new FontFace(
                        this.font,
                        "url(" + this.fontUrl + ")"
                    );
                    await font.load();
                    document.fonts.add(font);
                } else {
                    // all h5
                    var newStyle = document.createElement("style");
                    newStyle.appendChild(
                        document.createTextNode(
                            "\
                        @font-face {\
                        font-family: '" +
                                this.font +
                                "';\
                        src: url('" +
                                this.fontUrl +
                                "');\
                        }\
                    "
                        )
                    );
                    document.head.appendChild(newStyle);
                }
            }
        },
        getDataPng() {
            return this.$refs.canvas.toDataURL();
        },
        async getBlob() {
            this.loading = true;
            let blob = null;
            // let ts = new Date();
            try {
                blob = await new Promise(o => {
                    this.$refs.canvas.toBlob(o);
                });
            } finally {
                this.loading = false;
                //toBlob console
                // window.console.log("blob time:", new Date() - ts);
            }
            return blob;
        }
    },
    mounted() {
        this.canvasCtx = this.$refs.canvas.getContext("2d");
        this.refresh();
    }
};
</script>