<script>
import { Editlist } from "jsugc-websdk";
import ClipBtn from "@/components/ClipBtn";
import HScrollBar from "@/components/HScrollBar";
import OverlayToolTab from "@/views/Bottom/OverlayToolTab";
import Const from "@/const";

let __tabid = 0;
let TabID = {
    VideoAdd: __tabid++,
    VideoRemove: __tabid++,
    VideoRotate: __tabid++,
    VideoSort: __tabid++,
    VideoSplit: __tabid++,
    VideoVFlip: __tabid++,
    VideoHFlip: __tabid++,
    VideoBase: __tabid++,

    AddImage: __tabid++,
    AddLocalImage: __tabid++,
    AddText: __tabid++,
    AudioBase: __tabid++,
    BGM: __tabid++,
    Face: __tabid++,
    Filter: __tabid++,
    Mosic: __tabid++,
    AudioEffect: __tabid++,
    AspectRatio: __tabid++,

    ImageTab: __tabid++,
    TextTab: __tabid++,
    AudioTab: __tabid++,
    OtherTab: __tabid++,

    ToolEdit: __tabid++,
    ToolTemplate: __tabid++,
    ToolSettings: __tabid++,
    ToolRemove: __tabid++,
    ToolDone: __tabid++,
};

export default {
    TabID,
    components: {
        ClipBtn,
        OverlayToolTab,
        HScrollBar,
    },
    props: ["activeItem"],
    data() {
        return {
            TabID,
            toolTab: "cut",
        };
    },
    watch: {
        activeItem(o) {
            if (o) {
                if (o.type == Const.Overlay.Type.Text) {
                    this.toolTab = "text";
                } else if (
                    o.type == Const.Overlay.Type.Image ||
                    o.type == Const.Overlay.Type.Mosic
                ) {
                    this.toolTab = "image";
                }
            }
        },

        toolTab(v) {
            if (v == "image") this.$emit("select", TabID.ImageTab);
            else if (v == "text") this.$emit("select", TabID.TextTab);
            else if (v == "music") this.$emit("select", TabID.AudioTab);
            else this.$emit("select", TabID.OtherTab);
        },
    },
    computed: {
        isCurTimelineRemovable() {
            let timelines = Editlist.editlist.timelines;
            if (timelines.length <= 1) {
                return false;
            }
            let hasEnding = timelines[timelines.length - 1].ending;
            if (hasEnding) {
                let cur_id = Editlist.editlist.cur_timeline_id;
                let cur = Editlist.editlist.timelines.filter(
                    (o) => o.id == cur_id
                )[0];
                if (cur.ending) {
                    return true;
                }
                if (timelines.length <= 2) {
                    return false;
                }
            }
            return true;
        },
        curTimelineIsEnding() {
            let cur_id = Editlist.editlist.cur_timeline_id;
            let cur = Editlist.editlist.timelines.filter(
                (o) => o.id == cur_id
            )[0];
            return cur && cur.ending;
        },
    },
    methods: {
        select(tabID) {
            this.$emit("select", tabID);
        },
        overlayTabSelect(toolTabID, item) {
            let tabID;
            switch (toolTabID) {
                case OverlayToolTab.TabID.Edit:
                    tabID = TabID.ToolEdit;
                    break;
                case OverlayToolTab.TabID.Template:
                    tabID = TabID.ToolTemplate;
                    break;
                case OverlayToolTab.TabID.Settings:
                    tabID = TabID.ToolSettings;
                    break;
                case OverlayToolTab.TabID.Remove:
                    tabID = TabID.ToolRemove;
                    break;
                case OverlayToolTab.TabID.Done:
                    tabID = TabID.ToolDone;
                    break;
                default:
                    return;
            }
            this.$emit("select", tabID, item);
        },
    },
};
</script>