                    </div>
                </div>
                <!--  -->
                <div v-show="colorTabActive == ''">
                    <div class="stickerList nowrap colorList scroller">
                        <div class="colorItem none" @click="overlay.borderColor=null"></div>
                        <div
                            class="colorItem"
                            v-for="(item,idx) in ColorPreset"
                            :key="idx"
                            :style="{background:item}"
                            :class="{active:item==overlay.borderColor}"
                            @click="overlay.borderColor = item"
                        ></div>
                    </div>
                    <div class="bottomSlider">
                        <label class="label"></label>
                        <mu-slider
                            class="slider"
                            track-color="#666"
                            :max="5"
                            :min="1"
                            :step="0.01"
                            v-model="overlay.borderSize"
                        ></mu-slider>
                    </div>
                </div>
                <!--  -->
                <div v-show="colorTabActive == ''">
                    <div class="stickerList nowrap colorList scroller">
                        <div class="colorItem none" @click="overlay.shadowColor=null"></div>
                        <div
                            class="colorItem"
                            v-for="(item,idx) in ColorPreset"
                            :key="idx"
                            :style="{background:item}"
                            :class="{active:item==overlay.shadowColor}"
                            @click="overlay.shadowColor = item"
                        ></div>
                    </div>
                    <div class="bottomSlider">
                        <label class="label"></label>
                        <mu-slider
                            class="slider"
                            track-color="#666"
                            :max="10"
                            :min="0"
                            :step="0.01"
                            v-model="overlay.shadowLevel"
                        ></mu-slider>
                    </div>
                </div>
                <!--  -->
                <div v-show="colorTabActive == ''">
                    <div class="stickerList nowrap colorList scroller">
                        <div class="colorItem none" @click="overlay.backgroundColor=null"></div>
                        <div
                            class="colorItem"
                            v-for="(item,idx) in ColorPreset"
                            :key="idx"
                            :style="{background:item}"
                            :class="{active:item==overlay.backgroundColor}"
                            @click="overlay.backgroundColor = item"
                        ></div>
                    </div>
                    <div class="bottomSlider">
                        <label class="label"></label>
                        <mu-slider
                            class="slider"
                            track-color="#666"
                            :max="1"
                            :min="0"
                            :step="0.01"
                            v-model="overlay.backgroundTransparent"
                        ></mu-slider>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

