<script>
import TextRender from "@/views/Editor/TextRender";
// import ToolDialogTabs from "@/components/ToolDialogTabs";
// import ToolDialogTabPane from "@/components/ToolDialogTabPane";
// import CategoryTabs from "@/components/CategoryTabs";
// import CategoryTabPane from "@/components/CategoryTabPane";
import { UGCResourceManager as RManager } from "jsugc-websdk";
import { Editlist } from "jsugc-websdk";
import { Log } from "jsugc-websdk";
import ToolTab from "../ToolTab";
import Const from "@/const";
import Vue from "vue";
import "muse-ui-loading/dist/muse-ui-loading.css"; // load css
import Loading from "muse-ui-loading";
import extend from "extend";
import OpHistory from "@/model/History";
Vue.use(Loading);

let __tabid = 0;
const TabID = {
    Edit: __tabid++,
    Template: __tabid++,
    Settings: __tabid++,
};

let ColorPreset = [
    "#ffffff",
    "#ff0000",
    "#0000ff",
    "#00ff00",
    "#ff0000",
    "#FFC0CB",
    "#FFA500",
    "#777777",
    "#770000",
    "#000077",
    "#007700",
    "#777700",
    "#770077",
    "#775533",
];

export default {
    TabID,
    props: ["accept", "postermode", "options"],
    components: {
        TextRender,
        // ToolDialogTabs,
        // ToolDialogTabPane,
        // CategoryTabs,
        // CategoryTabPane
    },
    data() {
        let overlay = this.newOverlay();
        return {
            TabID,
            ColorPreset,
            Const,
            active: TabID.Edit,
            colorTabActive: 0,
            templateActive: 0,
            isApplyToAll: false,
            // 
            allTemplateWord: [],
            // 
            // allBubble: [],
            // 
            allBg: [],
            // 
            allFont: [],
            // text model
            overlay: overlay,
            overlayProps: Object.keys(overlay).filter((e) => e != "text"),
        };
    },
    watch: {
        options: "handleOptions",
        active() {
            this.categoryActive = 0;

            this.$nextTick(() => {
                if (this.active == TabID.Edit) {
                    this.$refs.input.focus();
                } else {
                    this.$refs.input.blur();
                }
            });
        },
        async accept(v) {
            let modified = true;
            if (v && this.overlayInitialState) {
                modified = Object.keys(this.overlayInitialState).find(
                    (k) => this.overlayInitialState[k] != this.overlay[k]
                );
            }

            if (v && modified && this.overlay.text) {
                let overlay = this.overlay;
                let isNew = !overlay.id;

                this.$emit("beforechange", {
                    overlay,
                });

                if (this.overlay.id == null)
                    overlay = Editlist.editlist.add_overlay(
                        this.overlay,
                        this.postermode
                    );

                let data = null;
                let doExport = async () => {
                    data = await this.$refs.textRender.getBlob();
                    if (data == null) {
                        // render
                        await new Promise((o) => {
                            this.$nextTick(() => doExport().then(o));
                        });
                        return;
                    }
                };
                await doExport();

                // 
                let dir = await RManager.getTempFolder();
                let fileName =
                    dir.name +
                    "/textImage_" +
                    overlay.id +
                    "_" +
                    Math.random().toString(16).replace("0.", "") +
                    ".png";
                let dataUrl = await this.createCacheFile(data, fileName);

                overlay.url = dataUrl + "?" + Math.random();

                this.overlay = overlay;
                // await this.applyToAll();
                Log.uilog("/tool/text", `save: ${overlay}`);
                this.$emit("change", {
                    overlay,
                });

                if (!this.postermode) {
                    if (isNew) {
                        OpHistory.createOperateHistory(
                            Const.Operate.TextAdd.Type,
                            overlay.id
                        );
                    } else {
                        OpHistory.createOperateHistory(
                            Const.Operate.TextModify.Type
                        );
                    }
                }
            }
            this.$parent.close();
        },
    },
    computed: {
        inputStyles() {
            // 3
            const lines = Math.min(this.overlay.text.split(/\n/).length, 3);
            return {
                height: (lines <= 1 ? 36 : lines * 20 + 8) + "px",
                "line-height": (lines <= 1 ? 36 : 20) + "px",
            };
        },
    },
    methods: {
        clearTemplate() {
            this.overlay.templateName = null;
            this.overlay.templateBgUrl = null;
            this.overlay.startX = 0;
            this.overlay.startY = 0;
            this.overlay.endX = 0;
            this.overlay.endY = 0;
            this.overlay.wordCount = 0;
        },
        async chooseTemplate(data) {
            this.templateName = data.name;
        },
        async chooseBubble(bubble) {
            if (window.requestFileSystem) {
                if (!bubble.local_file && !bubble.download) {
                    await RManager.downloadRes(RManager.ResType.fontbg, bubble);
                }
                if (bubble.local_file) {
                    this.overlay.templateBgUrl = bubble.local_file.toInternalURL();
                }
            } else {
                this.overlay.templateBgUrl = bubble.source_remote_url;
            }
            if (this.overlay.templateBgUrl) this.setBackground(bubble);
        },
        clearFont() {
            this.overlay.font = null;
            this.overlay.fontUrl = null;
        },
        setBackground(template) {
            this.overlay.templateName = template.name;
            this.overlay.startX = template.startX;
            this.overlay.startY = template.startY;
            this.overlay.endX = template.endX;
            this.overlay.endY = template.endY;
            this.overlay.wordCount = template.wordCount;
        },
        async chooseFont(font) {
            if (window.requestFileSystem) {
                if (!font.local_file) {
                    await RManager.downloadRes(RManager.ResType.font, font);
                }
                if (font.local_file) {
                    this.overlay.fontUrl = font.local_file.toInternalURL();
                }
            } else {
                this.overlay.fontUrl = font.source_remote_url;
            }
            if (this.overlay.fontUrl) this.overlay.font = font.family;
        },
        chooseAlign(align) {
            this.overlay.align = align;
        },
        newOverlay() {
            return {
                type: Const.Overlay.Type.Text,
                text: "",
                font: null,
                fontUrl: null,
                color: "grey",
                borderColor: null,
                shadowColor: null,
                backgroundColor: null,
                align: Const.Overlay.Align.Left,
                transparent: 0,
                borderSize: 1,
                shadowLevel: 1,
                backgroundTransparent: 0,
                // 
                templateName: "",
                templateBgUrl: null,
                //  
                startX: 0,
                startY: 0,
                endX: 0,
                endY: 0,
                wordCount: 0,
            };
        },
        updateOverlay(overlay) {
            this.overlay = overlay;
        },
        async createCacheFile(data, output) {
            if (!window.requestFileSystem) {
                // PC
                return URL.createObjectURL(data);
            }

            let fs = await RManager.filesystem();
            let file = await new Promise((o, x) => {
                fs.root.getFile(
                    output,
                    { create: true, exclusive: false },
                    o,
                    x
                );
            });
            let writer = await new Promise((o, x) => {
                file.createWriter(o, x);
            });

            await new Promise((o, x) => {
                writer.onwriteend = o;
                writer.onerror = x;
                writer.write(data);
            });

            return file.nativeURL;
        },

        handleOptions() {
            let v = this.options;
            if (v) {
                switch (v.operateToolTabId) {
                    case ToolTab.TabID.ToolEdit:
                        this.active = TabID.Edit;
                        break;
                    case ToolTab.TabID.ToolTemplate:
                        this.active = TabID.Template;
                        break;
                    case ToolTab.TabID.ToolSettings:
                        this.active = TabID.Settings;
                        break;
                    default:
                        break;
                }
            }
            if (!v || !v.overlay) {
                this.overlay = this.newOverlay();
                this.overlayInitialState = null;
            } else {
                this.updateOverlay(v.overlay);
                this.overlayInitialState = extend(true, {}, v.overlay);
            }
        },
        async applyToAll() {
            // 
            if (this.isApplyToAll) {
                let currOverlay = this.overlay;
                let allPromise = Editlist.editlist.overlay.map(
                    async (overlay) => {
                        if (
                            overlay.type == Const.Overlay.Type.Text &&
                            overlay.id != currOverlay.id
                        ) {
                            this.overlayProps.forEach((p) => {
                                overlay[p] = currOverlay[p];
                            });
                            this.overlay = overlay;
                            try {
                                this.$refs.textRender.refresh();
                                let data = await this.$refs.textRender.getBlob();
                                let dataUrl = await this.createCacheFile(
                                    data,
                                    "textImage_" + overlay.id + ".png"
                                );
                                overlay.url = dataUrl;
                            } catch (error) {
                                Log.error("create text image failed", error);
                            }
                        }
                    }
                );
                await Promise.all(allPromise);
            }
        },
    },
    async mounted() {
        this.handleOptions();
        this.$parent.acceptable();
        this.$refs.input.focus();

        this.allFont = await RManager.fetchResList(RManager.ResType.font);

        this.allBg = await RManager.fetchResList(RManager.ResType.fontbg);
        this.$emit("update:title", "");
    },
};
</script>
