<template>
    <div
        class="box"
        :class="{active:active, focus:focus, span:timeline.offset_sec}"
        :style="boxStyle"
        @touchend="toggleActive"
    >
        <mu-button
            flat
            class="left-tick"
            ref="left"
            @touchstart.stop.native="leftStart"
            @touchmove.stop.native="leftMove"
            @touchend.stop.native="leftEnd"
        >
            <mu-icon
                :class="{ dragging: leftDragging, outbound: leftOutBound }"
                value="keyboard_arrow_left"
            />
        </mu-button>
        <div class="content">
            <div class="thumb" :class="{overlay:overlay_mode}" :style="thumbStyle">
                <div v-if="timeline.ending" class="video-ending">
                    <span></span>
                </div>
                <TimelineItemThumb
                    v-else
                    :pps="pps"
                    :timeline="timeline"
                    :canload="canload_preview"
                />
            </div>
            <div class="border"></div>
        </div>
        <mu-button
            class="right-tick"
            ref="right"
            @touchstart.stop.native="rightStart"
            @touchmove.stop.native="rightMove"
            @touchend.stop.native="rightEnd"
        >
            <mu-icon
                :class="{ dragging: rightDragging, outbound: rightOutBound }"
                value="keyboard_arrow_right"
            />
        </mu-button>

        <div class="left-connect" v-if="!active">
            <mu-icon :size="12" value="insert_link" />
        </div>
    </div>
</template>
