</style>
<script>
import TimelineItemThumb from "./TimelineItemThumb";
import { Log } from "jsugc-websdk";
export default {
    components: { TimelineItemThumb },
    props: [
        "pps",
        "dragging",
        "active",
        "focus",
        "overlay_mode",
        "timeline",
        "min_sec",
        "canload_preview",
        // "left_drag_offset" //
    ],
    data() {
        return {
            // total_sec:0,
            // start_sec:0,
            // end_sec:0,
            // offset_sec:0,
            // active:false,
            // left_drag_offset:0,
            leftOutBound: false,
            rightOutBound: false,
            leftDragging: false,
            rightDragging: false,
            boxStyle: {
                width: 0,
                left: 0,
            },
            thumbStyle: {
                width: 0,
                left: 0,
            },
        };
    },
    watch: {
        "timeline.start_sec": "update",
        "timeline.total_sec": "update",
        "timeline.end_sec": "update",
        "timeline.offset_sec": "update",
        "timeline.left_drag_offset": "update",
    },
    methods: {
        update() {
            if (this.timeline.end_sec <= this.timeline.start_sec)
                this.timeline.end_sec = this.timeline.start_sec;
            this.boxStyle.width =
                (this.timeline.end_sec - this.timeline.start_sec) * this.pps +
                "px";
            this.boxStyle.left =
                (this.timeline.left_drag_offset + this.timeline.offset_sec) *
                    this.pps +
                "px";
            this.thumbStyle.width = this.timeline.total_sec * this.pps + "px";
            this.thumbStyle.left =
                this.timeline.start_sec * this.pps * -1 + "px";
        },
        toggleActive() {
            Log.uilog("/timeline/item", `active: ${this.timeline.id}`);
            // touchend  dragging 
            // click  touchend dragging  false
            if (this.dragging) return;
            this.$emit("active", !this.active);
        },
        leftStart(e) {
            this._pageX = e.changedTouches[0].pageX;
            this._value = this.timeline.start_sec;
            this.leftDragging = true;
            this.leftMove(e);
        },
        leftMove(e) {
            let offset_sec =
                (e.changedTouches[0].pageX - this._pageX) / this.pps;

            let start_sec = Math.min(
                this.timeline.end_sec - this.min_sec,
                Math.max(0, this._value + offset_sec)
            );

            if (
                start_sec == this.timeline.end_sec - this.min_sec ||
                start_sec == 0
            ) {
                this.leftOutBound = true;
            } else {
                this.leftOutBound = false;
            }

            this.changed({
                seek_sec: 0,
                start_sec,
                left_drag_offset: start_sec - this._value,
            });
            return true;
        },
        leftEnd() {
            Log.uilog(
                "/timeline/item",
                `modify startTime: ${this.timeline.start_sec}`
            );
            this.leftDragging = false;
            this.leftOutBound = false;
            this.changed();
        },
        rightStart(e) {
            this._pageX = e.changedTouches[0].pageX;
            this._value = this.timeline.end_sec;
            this.rightDragging = true;
            this.rightMove(e);
        },
        rightMove(e) {
            let offset_sec =
                (e.changedTouches[0].pageX - this._pageX) / this.pps;

            let end_sec = Math.min(
                this.timeline.total_sec,
                Math.max(
                    this.timeline.start_sec + this.min_sec,
                    this._value + offset_sec
                )
            );

            if (
                end_sec == this.timeline.start_sec + this.min_sec ||
                end_sec == this.timeline.total_sec
            ) {
                this.rightOutBound = true;
            } else {
                this.rightOutBound = false;
            }

            this.changed({
                seek_sec: end_sec - this.timeline.start_sec,
                end_sec,
            });
            return true;
        },
        rightEnd() {
            Log.uilog(
                "/timeline/item",
                `modify endTime: ${this.timeline.end_sec}`
            );
            this.rightDragging = false;
            this.rightOutBound = false;
            this.changed();
        },
        changed(opt) {
            let end = !opt;
            if (!opt)
                opt = {
                    left_drag_offset: 0,
                };

            this.$emit("change", {
                end,
                start_sec: this.timeline.start_sec,
                end_sec: this.timeline.end_sec,
                left_drag_offset: this.timeline.left_drag_offset,
                ...opt,
            });
        },
    },
    mounted() {
        this.update();
        if (!this.pps) {
            this.pps = screen.width / 10;
        }
    },
};
</script>