Number.prototype.toEven = function() {
    let ret = +this.toFixed(0);
    if (ret % 2 != 0) return ret - 1;
    else return ret;
};

let timelineIdSeq = 0;
let overlayIdSeq = 1; // //id, 0
// let amixIdSeq = 0;

async function timeline_format(timelines) {
    let ret = await Promise.all(
        timelines.map(async (val) => {
            if (val.total_sec) return val;

            let src = val.url;

            let format = await new Promise(
                UGCApp.ffmpeg.format.bind(null, src)
            );

            if (!format.duration) {
                // 
                throw new Error("");
            }

            if ((90 + format.video.rotate) % 180 == 0) {
                let _cacheWidth = format.video.width;
                format.video.width = format.video.height;
                format.video.height = _cacheWidth;
            }

            // offset_sec += format.duration/1000;
            return {
                url: src,
                src_rotate: format.video.rotate,
                width: format.video.width,
                height: format.video.height,
                start_sec: 0,
                end_sec: format.duration / 1000,
                total_sec: format.duration / 1000,
            };
        })
    );
    return ret;
}
