            Log.verbose("add timelines:", tl);

            tl = await timeline_format(tl);

            Log.verbose("add timelines with format:", tl);

            let timelines = tl.map((o) => {
                return {
                    id: this.get_next_timelineId(),
                    url: "",
                    thumbs: [],
                    active: false,
                    left_drag_offset: 0,
                    focus: false,
                    width: 0,
                    height: 0,
                    start_sec: 0,
                    offset_sec: 0,
                    end_sec: 0,
                    total_sec: 0,
                    rotate: 0,
                    vflip: 0,
                    hflip: 0,
                    eq: {
                        brightness: 0,
                        contrast: 1,
                        saturation: 1,
                        gamma: 1,
                    },
                    // gettergetRotatedWidth
                    // get rotated_width(){
                    //     if(this.rotate % 180 == 0)return this.width;
                    //     else return this.height;
                    // },
                    // get rotated_height(){
                    //     if(this.rotate % 180 == 0)return this.height;
                    //     else return this.width;
                    // },
                    ...o,
                };
            });

            if (
                this.timelines.length &&
                this.timelines[this.timelines.length - 1].ending
            ) {
                timelines = [
                    ...this.timelines.slice(0, this.timelines.length - 1),
                    ...timelines,
                    this.timelines[this.timelines.length - 1],
                ];
            } else {
                timelines = [...this.timelines, ...timelines];
            }

            timelines.reduce((pre, nxt, idx) => {
                if (idx == 1) {
                    pre.offset_sec = 0;
                    pre = pre.end_sec - pre.start_sec;
                }
                nxt.offset_sec = pre;
                return nxt.offset_sec + nxt.end_sec - nxt.start_sec;
            });

