<template>
    <div class="controlBox">
        <mu-icon
            value="aspect_ratio"
            @click="$emit('event',{eventID:EventID.AspectRatio})"
            style="position:absolute;left:0"
        />
        <mu-icon value="skip_previous" @click="$emit('event',{eventID:EventID.SeekBegin})" />
        <mu-button
            v-loading="loading"
            style="padding:0;overflow:visible"
            data-mu-loading-size="20"
            data-mu-loading-overlay-color="rgba(0, 0, 0, 0)"
            data-mu-loading-color="white"
            icon
            v-show="!playing"
            @click="$emit('event',{eventID:EventID.Play})"
        >
            <mu-icon value="play_arrow" />
        </mu-button>
        <mu-button v-show="playing" icon @click="$emit('event',{eventID:EventID.Pause})">
            <mu-icon value="pause" />
        </mu-button>
        <mu-icon value="skip_next" @click="$emit('event',{eventID:EventID.SeekEnd})" />
        <mu-icon
            value="undo"
            :class="[hasHistory ? '' : 'disabled']"
            @click="hasHistory && $emit('event',{eventID:EventID.Undo})"
            style="position:absolute;right:0"
        />
    </div>
</template>
<style lang="less" scoped>
.controlBox {
    text-align: center;
    color: white;
    font-size: 25px;

    & > * {
        padding: 10px 20px;
    }
}
.disabled {
    opacity: 0.5;
}
</style>
<script>
// import { Editlist } from "jsugc-websdk";
import OpHistory from "@/model/History";

let EventID = {
    Play: 0,
    Pause: 1,
    SeekBegin: 2,
    SeekEnd: 3,
    AspectRatio: 4,
    Undo: 5
};
export default {
    EventID,
    props: ["playing", "loading"],
    data() {
        return {
            EventID,
            history: OpHistory
        };
    },
    computed: {
        hasHistory() {
            return this.history.operateHistory.length > 1;
        }
    },
    created() {
        // Editlist.editlist.operate.history
        // Editlist.editlist.operate = this.editlist.operate;
    }
};
