<template>
    <div class="native-video-main" :class="{show:showscreen}"></div>
</template>
<style scoped>
.native-video-main {
    background: black !important;
}
.native-video-main.show {
    background: transparent !important;
}
</style>
<script>
/***
 * src, play, pause, seek
 *
 * position, duration
 *
 * meta, timeupdate
 */
import cordovaModel from "../model/CordovaModel";
import ResizeObserver from "resize-observer-polyfill";
import throttle from "lodash.throttle";
import Log from "../model/Log";

let EventID = {
    EnterForeground: -1,
    Meta: 1,
    Render: 2,
    Timeupdate: 3,
    Finished: 4,
    Error: 5,
    End: 6,
    SeekDone: 7,
    toString(id) {
        for (let i in EventID) {
            if (EventID[i] == id) return i;
        }
    },
};

let FFPlay = {
    rect() {},
    play() {},
    running() {},
    pause() {},
    stop() {},
    seek() {},
    duration() {},
    position() {},
    mute() {},
    thumb() {},
    refresh() {},
    setEventCallback() {},
};

export default {
    data() {
        return {
            cmd: "",
            ready: false,
            playing: false,
            loaded: false,
            keep_screen: false,
            lastPosition: 0,
            timeupdateTmr: null,
            seeking: false,
            throttle_ing: false,
        };
    },
    computed: {
        showscreen() {
            return true; // this.loaded || this.keep_screen;
        },
    },
    watch: {
        playing(p) {
            this.$emit("playing", p);
        },
    },
    methods: {
        async resize() {
            let rect = this.$el.getBoundingClientRect();
            Log.verbose(`[player] resize: ${JSON.stringify(rect)}`);
            await this.rect(rect);
            this.$emit("resize");
        },
        async rect(_rect) {
            let rect = {
                x: _rect.left,
                y: _rect.top,
                width: _rect.right - _rect.left,
                height: _rect.bottom - _rect.top,
            };

            if (!rect.width || !rect.height) return;

            Log.verbose(
                "[player] update player rect: " +
                    [rect.x, rect.y, rect.width, rect.height].join(",")
            );
            await new Promise(
                FFPlay.rect.bind(null, rect.x, rect.y, rect.width, rect.height)
            );

            // android: 
            await new Promise((o) => setTimeout(o, 150));
            await new Promise(FFPlay.refresh.bind(null));
        },
        async reload(cmd) {
            Log.verbose(`[player] reload: ${cmd.replace("ffplay", "")}`);
            FFPlay.command("reload|ffplay", cmd);
        },
        async load(cmd) {
            await this.stop();
            this.ready = false;
            this.playing = false;
            this.loaded = false;
            this.seeking = false;
            this.throttle_ing = false;
            this.cmd = cmd;

            FFPlay.setEventCallback(this.onEvent);
            if (cmd) {
                Log.info("[player] load player");
                Log.verbose(
                    "reload player param: " + cmd.replace("ffplay", "")
                );
                this.$emit("loading");

                await new Promise(FFPlay.play.bind(null, cmd));

                await new Promise((o, x) => {
                    let timeout = 15 * 1000;
                    let i = setInterval(() => {
                        timeout -= 100;
                        if (timeout < 0) {
                            clearInterval(i);
                            x("failed to wait NativePlayer.load");
                        } else if (this.loaded) {
                            clearInterval(i);
                            o();
                        }
                    }, 100);
                });
            }
        },
        async running() {
            return await new Promise(FFPlay.running);
        },
        resume() {
            this.playing = true;
            FFPlay.mute(0);
            FFPlay.resume();
            Log.verbose(`[player] resume`);
        },
        pause() {
            this.playing = false;
            FFPlay.mute(1);
            FFPlay.pause();
            Log.verbose(`[player] pause`);
        },
        async stop(keep_screen) {
            this.playing = false;
            Log.verbose(`[player] stop`);
            let ret = await new Promise(FFPlay.stop.bind(null, !!keep_screen));
            return ret;
        },
        async doSeek2(pos) {
            if (pos == -1) pos = await this.duration();

            this.lastPosition = pos;
            this.playing = false;
            this.$emit("loading");
            FFPlay.seek(pos);
            this.throttle_ing = false;
        },
        doSeek: throttle(async function (sec) {
            if (sec == -1) sec = await this.duration();

            this.lastPosition = sec;
            this.playing = false;
            this.$emit("loading");
            FFPlay.seek(sec);

            this.throttle_ing = false;
        }, 100),
        seek(sec) {
            this.seeking = true;
            this.throttle_ing = true;
            this.doSeek(sec);
        },
        async duration() {
            return await new Promise(FFPlay.duration);
        },
        async position() {
            return await new Promise(FFPlay.position);
        },
        onResume() {
            FFPlay.refresh();
        },
        checkAndSendLoaddone() {
            if (this.loaded && !this.seeking && !this.throttle_ing) {
                this.$emit("loaddone");
            }
        },
        async onEvent(event) {
            Log.verbose("got player event: " + EventID.toString(event));
            switch (event) {
                case EventID.EnterForeground:
                    {
                        //  ugc activity reload
                        //  SDL
                        // this.$emit("needreload");
                    }
                    break;
                case EventID.Meta:
                    {
                        // rect 
                        // await new Promise( o=>setTimeout(o, 500));
                        // FFPlay.refresh();
                        this.$emit("meta", {
                            duration: await this.duration(),
                        });
                    }
                    break;
                case EventID.Render:
                    {
                        if (!this.loaded) {
                            await new Promise((o) => setTimeout(o, 100));
                            await this.resize();
                            this.loaded = true;
                        }
                        this.$emit("render");
                        this.checkAndSendLoaddone();
                    }
                    break;
                case EventID.Timeupdate:
                    {
                        // this.$emit("timeupdate",
                        //     await this.position()
                        // );
                    }
                    break;
                case EventID.Finished:
                    {
                        this.$emit("finished");
                        if (this.seeking && !this.throttle_ing) {
                            this.seeking = false;
                            this.$emit("seekdone");
                        }
                        this.checkAndSendLoaddone();
                    }
                    break;
                case EventID.End:
                    {
                        this.loaded = false;
                        this.$emit("end");
                    }
                    break;
                case EventID.SeekDone:
                    {
                        if (!this.throttle_ing) this.seeking = false;
                        this.$emit("seekdone");
                        this.checkAndSendLoaddone();
                    }
                    break;
            }
        },
    },
    mounted() {
        if (cordovaModel.isCordova()) {
            FFPlay = UGCApp.ffplay;
            this.resize();

            document.addEventListener("resume", this.onResume, false);
        }

        this._resizeOb = new ResizeObserver(() => {
            this.resize();
        });

        this._resizeOb.observe(this.$el);
    },
    beforeDestroy() {
        document.removeEventListener("resume", this.onResume, false);
        this._resizeOb.disconnect();
    },
};
</script>