<template>
    <div class="ending-main">
        <PosterMakerTop @close="close" @save="rendering=true" />
        <div class="title"></div>
        <div ref="mainBox" class="main-box">
            <div style="text-align:center">
                <div class="avatar" @click="onAvatarClick">
                    <span v-if="!avatarUrl" class="material-icons">account_circle</span>
                    <img :src="avatarUrl" v-else @error="avatarLoadError" />
                    <div class="text"></div>
                </div>
            </div>
            <div class="input">
                <input v-model="ending.nickName" placeholder="" maxlength="18" />
            </div>
            <div class="input">
                <input v-model="ending.desc" placeholder="" maxlength="23" />
            </div>
        </div>
        <EndingRender
            v-if="rendering"
            ref="render"
            @rendered="save"
            @error="renderError"
            :avatar-file="ending.avatarFile"
            :nick-name="ending.nickName || nickNamePlaceholder"
            :desc="ending.desc"
        />
        <Crop
            v-if="cropping"
            ref="crop"
            @close="cropping = false; cropAvatarUrl=''"
            @save="cropSave"
            :src="cropAvatarUrl"
            :ratio="1"
        />
        <mu-bottom-sheet :overlay-close="true" :esc-press-close="true" :open.sync="isMenuOpen">
            <mu-list @item-click="isMenuOpen=false">
                <mu-sub-header></mu-sub-header>
                <mu-list-item button @click="pickImage">
                    <mu-list-item-action>
                        <mu-icon value="folder_open" color="orange"></mu-icon>
                    </mu-list-item-action>
                    <mu-list-item-title style="color:#f44336"></mu-list-item-title>
                </mu-list-item>
                <mu-list-item button @click="deleteAvatar">
                    <mu-list-item-action>
                        <mu-icon value="delete" color="orange"></mu-icon>
                    </mu-list-item-action>
                    <mu-list-item-title></mu-list-item-title>
                </mu-list-item>
            </mu-list>
        </mu-bottom-sheet>
    </div>
</template>

<style lang="less" scoped>
.title {
    color: white;
    text-align: center;
}

.ending-main {
    background-color: #333;
    position: relative;
    width: 100%;
    height: 100%;
    padding-bottom: 20px;
}

.avatar {
    margin-top: 30px;
    text-align: center;
    overflow: hidden;
    display: inline-block;

    .material-icons {
        font-size: 80px;
        color: #555;
    }

    img {
        border-radius: 50%;
        width: 80px;
        height: 80px;
    }
    .text {
        margin-top: 10px;
        text-align: center;
        color: #999;
    }
}

.input {
    padding: 20px;

    input {
        line-height: 1;
        width: 100%;
        padding: 12px 12px;
        border-radius: 6px;
        background: #444;
        border: 0;
        color: #bbb;
    }
}
</style>

<script>
import PosterMakerTop from "@/views/Top/PosterMakerTop";
import Crop from "@/components/Crop";
import { Editlist } from "jsugc-websdk";
import { UGCResourceManager } from "jsugc-websdk";
import { CordovaModel } from "jsugc-websdk";
import { Log } from "jsugc-websdk";
import Const from "@/const";
import EndingRender from "@/views/Editor/EndingRender";
import OpHistory from "@/model/History";

export default {
    components: {
        PosterMakerTop,
        EndingRender,
        Crop,
    },
    data() {
        return {
            rendering: false,
            editlist: null,
            avatarUrl: null,
            cropAvatarUrl: null,
            isMenuOpen: false,
            cropping: false,
            nickNamePlaceholder: "",
            ending: {
                avatarFile: "",
                nickName: "",
                desc: "",
            },
        };
    },
    methods: {
        avatarLoadError() {
            this.avatarUrl = "";
        },
        onAvatarClick() {
            if (this.ending.avatarFile) {
                this.isMenuOpen = true;
            } else {
                this.pickImage();
            }
        },
        deleteAvatar() {
            this.ending.avatarFile = "";
            this.ending.url = "";
            this.avatarUrl = "";
        },
        async pickImage() {
            Log.uilog("/ending", "pick image");
            // native cordova.camera plugin  content:// 
            // CameraLauncher.java::processResultFromGallery::707  fileLocation
            let file = await new Promise((o, x) =>
                navigator.camera.getPicture(o, x, {
                    sourceType: Camera.PictureSourceType.PHOTOLIBRARY,
                    allowEdit: false,
                    mediaType: Camera.MediaType.PICTURE,
                    destinationType: Camera.DestinationType.NATIVE_URI,
                })
            );

            if (!file) return;

            this.cropAvatarUrl = await this.fileToImageUrl(file);
            this.cropping = true;

            Log.uilog("/ending", `pick image done: ${this.cropAvatarUrl}`);
        },
        async save() {
            Log.uilog("/ending", "save");
            let ending = {
                avatarFile: this.ending.avatarFile,
                nickName: this.ending.nickName,
                desc: this.ending.desc,
                url: null,
            };
            UGCResourceManager.saveLastEnding(ending);

            let dir = await UGCResourceManager.getTempFolder();
            let fileName =
                dir.name +
                "/endingImage_" +
                Math.random().toString(16).replace("0.", "") +
                ".png";

            try {
                let data = await this.$refs.render.getBlob();
                let outFile = await this.createCacheFile(data, fileName);
                ending.url = outFile;
                Log.verbose("created ending file: " + outFile);

                let curEnding = Editlist.editlist.timelines.find(
                    (e) => e.ending
                );
                if (curEnding) {
                    Object.assign(curEnding, ending);
                } else {
                    await Editlist.editlist.add_timelines([
                        {
                            start_sec: 0,
                            end_sec: 2,
                            total_sec: 2,
                            ending: true,
                            ...ending,
                        },
                    ]);
                }

                this.$emit("close");

                OpHistory.createOperateHistory(Const.Operate.EndingModify.Type);
            } catch (error) {
                this.$snotify.error("");

                this.$emit("close");
            }
            this.rendering = false;
        },
        async renderError(error) {
            this.rendering = false;
            this.$snotify.error("");
            window.console.error(error);
        },
        async close() {
            Log.uilog("/ending", "cancel");
            this.$emit("close");
        },
        async fileToImageUrl(file) {
            return await UGCResourceManager.assetReadAsDataUrl(
                await CordovaModel.getCdvUrl(file)
            );
        },
        async cropSave(data) {
            // let dir = await UGCResourceManager.getTempFolder() ;
            // 
            let dir = await UGCResourceManager.getExportPosterFolder();
            let fileName =
                dir.name +
                "/avatarCropImage_" +
                Math.random().toString(16).replace("0.", "") +
                ".png";

            let outFile = await this.createCacheFile(data, fileName);
            this.avatarUrl = await this.fileToImageUrl(outFile);
            this.ending.avatarFile = outFile;
            this.cropping = false;
        },
        async createCacheFile(data, output) {
            Log.uilog("/ending", "begin create image");
            if (!window.requestFileSystem) {
                // PC
                return URL.createObjectURL(data);
            }

            let fs = await UGCResourceManager.filesystem();
            let file = await new Promise((o, x) => {
                fs.root.getFile(
                    output,
                    { create: true, exclusive: false },
                    o,
                    x
                );
            });
            let writer = await new Promise((o, x) => {
                file.createWriter(o, x);
            });

            await new Promise((o, x) => {
                writer.onwriteend = o;
                writer.onerror = x;
                writer.write(data);
            });

            Log.uilog("/ending", "create image done");
            return file.nativeURL;
        },
    },
    async mounted() {
        Log.uilog("/ending", "enter");

        let curEnding = Editlist.editlist.timelines.find((e) => e.ending);
        if (!curEnding) {
            curEnding = UGCResourceManager.getLastEnding();
        }
        if (curEnding) {
            this.ending = { ...curEnding };
            this.avatarUrl = await this.fileToImageUrl(this.ending.avatarFile);
        }
        document.addEventListener("backbutton", this.close, false);
    },
    async beforeDestroy() {
        document.removeEventListener("backbutton", this.close, false);
    },
};
</script>