<template>
    <div class="image-main">
        <ScrollTab
            :value.sync="active"
            color="transparent"
            indicator-color="red"
            text-color="white"
            left
        >
            <mu-tab v-for="(item,idx) in allSticker" :key="idx">{{item.name}}</mu-tab>
        </ScrollTab>
        <div class="tab-box" v-show="active == idx" v-for="(folder,idx) in allSticker" :key="idx">
            <div
                v-for="(item) in folder.list"
                :key="item.name"
                @click="chooseSticker(item)"
                class="stickerItem"
            >
                <img
                    loop="0"
                    alt=" "
                    :src="item.thumb_remote_url+'/'+item.source+'.thumb.apng'"
                    @error.once="$event.target.src = item.thumb_rel_url + item.source + '.thumb.apng'"
                />
                <mu-icon
                    v-if="!item.local_file&&!item.download"
                    class="downloadmu-icon"
                    value="arrow_downward"
                />
                <mu-circular-progress
                    class="loadingmu-icon"
                    v-if="item.download"
                    :value="item.download.percent"
                    :size="60"
                    style="zoom:0.2;margin-left:10px"
                    color="white"
                    :stroke-width="10"
                />
            </div>
        </div>
    </div>
</template>
<style lang="less" scoped>
.image-main {
    display: flex;
    flex-flow: column;
}
.tab-box {
    width: 100%;
    padding: 5px 10px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.stickerItem {
    width: 80px;
    height: 50px;
    position: relative;
    color: white;
    flex: 0 0 15%;
    margin: 10px;

    .downloadmu-icon {
        position: absolute;
        right: 0;
        bottom: 0;
        border-radius: 10px;
        background: rgba(255, 0, 0, 0.3);
        font-size: 10px;
    }

    .loadingmu-icon {
        position: absolute;
        right: 0;
        bottom: 0;
    }

    img {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}
</style>
<script>
import { UGCResourceManager as RManager } from "jsugc-websdk";
import { Editlist, Log } from "jsugc-websdk";
import ScrollTab from "@/components/ScrollTab";
import Const from "@/const";
import OpHistory from "@/model/History";
export default {
    props: ["postermode"],
    components: {
        ScrollTab,
    },
    data() {
        return {
            active: 0,
            allSticker: [],
        };
    },
    methods: {
        async chooseSticker(item) {
            if (!item.local_file && !item.download) {
                RManager.downloadRes(
                    RManager.ResType.image,
                    item,
                    (percent) => {
                        item.download.percent = Math.ceil(percent * 100);
                    }
                );
                return;
            }

            let canAdd =
                Editlist.editlist.overlay.filter((o) => o.new).length == 0;
            if (item.local_file && canAdd) {
                this.$emit("beforechange", {
                    overlay,
                });

                let overlay = Editlist.editlist.add_overlay(
                    {
                        end_sec: +item.duration,
                        url: item.local_file.toURL(),
                        type: Const.Overlay.Type.Image,
                    },
                    this.postermode
                );
                Log.uilog("/tool/overlay", `choose ${JSON.stringify(overlay)}`);
                this.$emit("change", {
                    overlay,
                });

                OpHistory.createOperateHistory(
                    Const.Operate.ImageAdd.Type,
                    overlay.id
                );

                this.$parent.close();
            }
        },
    },
    async mounted() {
        this.allSticker = await RManager.fetchResList(RManager.ResType.image);
        this.$emit("update:title", "");
    },
};
</script>