<template>
    <HScrollBar :scroller="scroller" class="scroll-tab">
        <mu-tabs
            ref="tab"
            v-bind="$attrs"
            @update:value="$emit('update:value',$event)"
            @change="fixTab($event);$emit('change',$event)"
        >
            <slot></slot>
        </mu-tabs>
    </HScrollBar>
</template>
<style>
.scroll-tab {
    width: 100%;
}
.scroll-tab .mu-tabs {
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
}
</style>
<script>
import HScrollBar from "./HScrollBar";

export default {
    components: { HScrollBar },
    data() {
        return {
            scroller: null
        };
    },
    methods: {
        fixTab(value) {
            // mu-tabs
            let tabDom = this.$refs.tab.$el;
            let lineDom = tabDom.querySelector(".mu-tab-link-highlight");
            let left = tabDom.children[value].offsetLeft;
            tabDom.children[value].scrollIntoView(true);
            this.$nextTick(() => {
                let cssText = lineDom.style.cssText;
                lineDom.style.cssText = cssText.replace(
                    /(translate\().*,/,
                    "$1" + left + "px,"
                );
            });
        }
    },
    mounted() {
        this.$nextTick(() => {
            this.scroller = this.$refs.tab.$el;
        });
    }
};
</script>