<template>
    <div>
        <slot :progress="exportProgress" :error="error"></slot>
    </div>
</template>
<style scoped>
</style>
<script>
import Editlist from "../model/Editlist";
import CordovaModel from "../model/CordovaModel";
import Log from "../model/Log";

let Transcode = {
    async thumb(src, des, ss, duration, span) {
        let timebase = 1000000;
        // TODO 
        return await new Promise(
            UGCApp.ffmpeg.thumb.bind(
                null,
                src,
                des,
                ss * timebase,
                duration * timebase,
                span * timebase
            )
        );
    },
};

export default {
    Transcode,
    props: [],
    data() {
        return {
            error: "",
            exportProgress: 0,
        };
    },
    methods: {
        async begin() {
            let config = await CordovaModel.getConfig();
            if (!config.output) {
                // 
                config.output = cordova.file.cacheDirectory + "output.mp4";
                CordovaModel.setConfig(config);
                Log.info(
                    `config.output not set, use default: ${config.output}`
                );
            }

            // SDK
            let des = config.output.replace("file://", "");

            let cmd = Editlist.getExportParam(des);
            Log.info("begin export");
            Log.verbose(`begin export with param: ${cmd}`);
            let ret = await new Promise(UGCApp.ffmpeg.start.bind(null, cmd));
            if (ret) {
                this.error = "" + ret;
                Log.error(`export failed: ${this.error}`);
                this.$emit("error", {
                    code: ret,
                    message: this.error,
                });
                return;
            }

            let total_sec = Editlist.editlist.total_sec;
            let progress_sec = 0;
            let status;
            let progress;

            await new Promise((o) => {
                setTimeout(o, 1000);
            });

            do {
                status = await new Promise(UGCApp.ffmpeg.status);
                progress = await new Promise(UGCApp.ffmpeg.progress);
                progress_sec = progress.pts / 1000;

                this.exportProgress = +Math.max(
                    0,
                    Math.min(100, (progress_sec / total_sec) * 100)
                ).toFixed(1);

                this.$emit("progress", this.exportProgress);

                await new Promise((o) => {
                    setTimeout(o, 500);
                });
                Log.verbose(
                    "export progress:" + progress.pts + ", status:" + status
                );
            } while (status == 1);

            let result = await new Promise(UGCApp.ffmpeg.report);

            if (result == 0) {
                let desSrc;
                if (CordovaModel.isIOS()) {
                    // await new Promise((o, x) => UGCApp.sendToDCIM(des, o, x));
                    desSrc = (await CordovaModel.getCdvUrl(des)).replace(
                        "cdvfile:",
                        "http:"
                    );
                } else {
                    // await new Promise((o, x) => UGCApp.sendToDCIM(des, o, x));
                    desSrc = await CordovaModel.getCdvUrl(des);
                }

                // export poster
                try {
                    if (!config.output_thumb) {
                        // 
                        config.output_thumb =
                            cordova.file.cacheDirectory + "output.jpg";
                        CordovaModel.setConfig(config);
                        Log.info(
                            `config.output_thumb not set, use default: ${config.output_thumb}`
                        );
                    }
                    // appcahce/poster/
                    await this.copyPosterTo(config.output_thumb);
                } catch (error) {
                    this.error = "";
                    Log.error(`export poster failed: ${this.error}`);
                    this.$emit("error", {
                        code: result,
                        message: this.error,
                    });
                }

                this.$emit("success", desSrc);
            } else {
                this.error = "";
                Log.error(`export failed: ${this.error}, ret:${result}`);
                this.$emit("error", {
                    code: result,
                    message: this.error,
                });
            }
        },
        async cancel() {
            await new Promise(UGCApp.ffmpeg.cancel);
        },
        //  cache/tmpcache/poster
        async copyPosterTo(outputFile) {
            let posterUrl = Editlist.editlist.poster.url;
            if ("/" == posterUrl[0]) {
                posterUrl = "file://" + posterUrl;
            }
            if ("/" == outputFile[0]) {
                outputFile = "file://" + outputFile;
            }

            let posterFileEntity = await new Promise(
                resolveLocalFileSystemURL.bind(window, posterUrl)
            );

            // create export dir
            let outDirEntity = null;
            let outDir = outputFile.split("/").slice(0, -1).join("/");
            let outName = outputFile.split("/").reverse()[0];

            try {
                let outFileEntity = await new Promise(
                    resolveLocalFileSystemURL.bind(window, outputFile)
                );
                outFileEntity.remove();
            } catch (e) {
                //
            }

            outDirEntity = await new Promise(
                resolveLocalFileSystemURL.bind(window, outDir)
            );

            // copy poster
            return await new Promise(
                posterFileEntity.copyTo.bind(
                    posterFileEntity,
                    outDirEntity,
                    outName
                )
            );
        },
        async createPoster() {
            let dir = await UGCRes.getTempFolder();

            let des =
                dir.nativeURL +
                "/poster_" +
                String(Math.random()).replace("0.", "") +
                ".jpg";
            des = des.replace("file://", "");
            let ret = await new Promise(UGCApp.ffplay.thumb.bind(null, des));
            if (!ret) {
                Log.error("make poster error:" + ret);
                // return;
            }
            this.posterUrl = des;
            this.isTempPoster = true;
        },
    },
    async mouted() {
        await new Promise(UGCApp.ffplay.stop.bind(null, false));
    },
    beforeDestroy() {
        this.cancel();
    },
};
</script>