</style>
<script>
import Const from "@/const";
import { Editlist, Log } from "jsugc-websdk";
import Magnet from "@/model/Magnet";

const MIN_SPAN = 0.1;

export default {
    props: ["pps", "active", "end_sec", "offset_sec", "is_amix", "type", "id"],
    data() {
        return {
            overallDragging: false,
            leftDragging: false,
            rightDragging: false,
            boxStyle: {
                width: 0,
                left: 0,
            },
        };
    },
    watch: {
        end_sec: "update",
        offset_sec: "update",
    },
    computed: {
        typeIcon() {
            return this.is_amix
                ? "music_note"
                : Const.Overlay.Type.Text == this.type
                ? "font_download"
                : Const.Overlay.Type.Image == this.type
                ? "image"
                : "blur_on";
        },
        typeText() {
            return this.is_amix
                ? ""
                : Const.Overlay.Type.Text == this.type
                ? ""
                : Const.Overlay.Type.Image == this.type
                ? ""
                : "";
        },
    },
    methods: {
        fmtSeconds(v) {
            let s = (v % 60).toFixed(0);
            let m = ((v / 60) % 60).toFixed(0);
            let h = ((v / 60 / 60) % 60).toFixed(0);
            return h + ":" + m + ":" + s;
        },

        update() {
            if (this.end_sec <= 0) this.end_sec = MIN_SPAN;
            this.boxStyle.width = this.end_sec * this.pps + "px";
            this.boxStyle.left = this.offset_sec * this.pps + "px";
        },
        toggleActive() {
            Log.uilog("/timeline/overlay", `active: ${this.id}`);
            if (!this.active) {
                this.$nextTick(() => {
                    // focus
                    this.$el.focus({ preventScroll: true });
                });
                this.$emit("active", !this.active);
            }
        },
        dragStart(e) {
            this._pageX = e.changedTouches[0].pageX;
            this._value = this.offset_sec;
            this.overallDragging = true;
            this.dragMove(e);
        },
        dragMove(e) {
            let oldOffset = this._value;
            let offset_sec =
                (e.changedTouches[0].pageX - this._pageX) / this.pps;
            // to absolute
            offset_sec = oldOffset + offset_sec;

            // dock begin
            let oldLayout = this.createMagnetLayout({
                left: oldOffset,
                width: this.end_sec,
            });
            let newLayout = this.createMagnetLayout({
                left: offset_sec,
                width: this.end_sec,
            });
            newLayout = this.magnet.check(oldLayout, newLayout);
            (offset_sec = newLayout.left),
                // dock end

                (offset_sec = Math.max(0, offset_sec));
            offset_sec = Math.min(Editlist.editlist.total_sec, offset_sec);

            this.changed({
                offset_sec,
                overallDragging: this.overallDragging,
            });
            return true;
        },
        dragEnd() {
            Log.uilog(
                "/timeline/overlay",
                `modify offsetTime: ${this.offset_sec}`
            );
            this.overallDragging = false;
            this.leftDragging = false;
            this.rightDragging = false;
            this.changed();
        },
        leftStart(e) {
            this._pageX = e.changedTouches[0].pageX;
            this._value = this.offset_sec;
            this._value2 = this.end_sec;
            this.leftDragging = true;
            this.leftMove(e);
        },
        leftMove(e) {
            let oldOffset = this._value;
            let oldEnd = this._value2;
            let offset_sec =
                (e.changedTouches[0].pageX - this._pageX) / this.pps;
            // to absolute
            offset_sec = oldOffset + offset_sec;

            // dock begin: leftwidth=0rightleft
            let oldLayout = this.createMagnetLayout({
                left: oldOffset,
                width: 0,
            });
            let newLayout = this.createMagnetLayout({
                left: offset_sec,
                width: 0,
            });
            newLayout = this.magnet.check(oldLayout, newLayout);
            offset_sec = newLayout.left;
            // dock end

            offset_sec = Math.max(0, offset_sec);
            offset_sec = Math.min(oldOffset + oldEnd - MIN_SPAN, offset_sec);

            let end_sec = Math.max(0, oldEnd + oldOffset - offset_sec);

            this.changed({
                offset_sec,
                end_sec,
            });
            return true;
        },
        rightStart(e) {
            this._pageX = e.changedTouches[0].pageX;
            this._value = this.end_sec;
            this.rightDragging = true;
            this.rightMove(e);
        },
        rightMove(e) {
            let oldEnd = this._value;
            let offset_sec =
                (e.changedTouches[0].pageX - this._pageX) / this.pps;
            let end_sec = oldEnd + offset_sec;

            // dock begin
            let oldLayout = this.createMagnetLayout({
                left: this.offset_sec + oldEnd,
                width: 0,
            });
            let newLayout = this.createMagnetLayout({
                left: this.offset_sec + end_sec,
                width: 0,
            });
            newLayout = this.magnet.check(oldLayout, newLayout);
            end_sec = newLayout.left - this.offset_sec;
            // dock end

            end_sec = Math.min(
                Editlist.editlist.total_sec - this.offset_sec,
                end_sec
            );
            end_sec = Math.max(MIN_SPAN, end_sec);

            this.changed({
                end_sec,
            });
            return true;
        },
        changed(opt) {
            let end = !opt;

            if (end)
                Log.uilog(
                    "/timeline/overlay",
                    `modify: ${JSON.stringify({
                        offset_sec: this.offset_sec,
                        end_sec: this.end_sec,
                    })}`
                );

            this.$emit("change", {
                end,
                offset_sec: this.offset_sec,
                end_sec: this.end_sec,
                ...opt,
            });
        },
        createMagnetLayout(layout) {
            return {
                left: layout.left,
                width: layout.width,
                right: layout.left + layout.width,
                top: 0,
                height: 0,
                bottom: 0,
            };
        },
        // 
        vibrate() {
            navigator.vibrate([100]);
        },
    },
    mounted() {
        this.update();
        if (!this.pps) {
            this.pps = screen.width / 10;
        }
        this.magnet = new Magnet({
            span: 6 / this.pps, // seconds
            features: ["inside", "outside"],
            render: (x) => {
                if (x != null) {
                    this.vibrate();
                }
            },
            dynamicLayouts: () => {
                let overlay = [];
                if (this.is_amix) {
                    overlay = Editlist.editlist.amix;
                } else if (
                    this.type === Const.Overlay.Type.Mosic ||
                    this.type === Const.Overlay.Type.Image
                ) {
                    overlay = [
                        ...Editlist.editlist.mosic,
                        ...Editlist.editlist.overlay.filter(
                            (e) => e.type == Const.Overlay.Type.Image
                        ),
                    ];
                } else {
                    overlay = Editlist.editlist.overlay.filter(
                        (e) => e.type == this.type
                    );
                }
                let layouts = overlay
                    .filter((o) => o.id != this.id)
                    .map((o) =>
                        this.createMagnetLayout({
                            left: o.offset_sec,
                            width: o.end_sec,
                        })
                    );
                // 
                if (!this.overallDragging) {
                    layouts.push({
                        left: Editlist.editlist.time_sec,
                        width: 0,
                    });
                }
                return layouts;
            },
        });
    },
};
</script>