<template>
    <NativeVideo
        ref="video"
        @resize="$emit('resize')"
        @meta="$emit('render')"
        @playing="$emit('playing',$event)"
        @finished="$emit('finished')"
        @render="$emit('render')"
        @loading="$emit('loading')"
        @loaddone="$emit('loaddone')"
        @timeupdate="$emit('timeupdate',$event)"
    />
</template>
<style scoped>
</style>
<script>
import Editlist from "../model/Editlist";
import NativeVideo from "../components/NativeVideo";
import UGCRes from "../model/UGCResourceManager";
import Log from "../model/Log";

let FFPlayInstance = null;

export default {
    components: { NativeVideo },
    props: ["pause", "postermode"],
    data() {
        return {};
    },
    watch: {
        pause(p) {
            if (p) this.$refs.video.pause();
            else this.$refs.video.resume();
        },
    },
    methods: {
        seek(sec) {
            if (sec === undefined) sec = Editlist.editlist.time_sec;
            this.$refs.video.seek(sec);
        },
        async stop() {
            this.loaded = false;
            await this.$refs.video.stop();
        },
        async makeThumb() {
            let dir = await UGCRes.getTempFolder();

            // 
            let fileName =
                "poster_" +
                Math.random().toString(16).replace("0.", "") +
                ".jpg";

            let des = dir.nativeURL + "/" + fileName;

            try {
                let ret = await this.thumb(des);
                if (ret) {
                    // 
                    await new Promise((o) => {
                        setTimeout(o, 500);
                    });
                    let ret = await this.$refs.preview.thumb(des);

                    if (ret) throw new Error("make poster error" + ret);
                }

                Editlist.editlist.poster.url = des + "?" + Math.random();
                Editlist.editlist.poster.rotate = 0;
            } catch (e) {
                this.$snotify.error("");
            }
        },
        async thumb(des) {
            let ret = await new Promise(
                UGCApp.ffplay.thumb.bind(null, des.replace("file://", ""))
            );
            return ret;
        },
        async position() {
            return await new Promise(UGCApp.ffplay.position);
        },
        async reload() {
            // ffplay  player 
            // ffplay 
            if (
                !(FFPlayInstance == this) ||
                !(await this.$refs.video.running())
            ) {
                return await this.load();
            }

            this.throttle_ing = this.seeking = false;

            let cmd = this.postermode
                ? Editlist.getPosterPlayerParam(true)
                : Editlist.getPlayerParam(true);

            await this.$refs.video.reload(cmd);
        },
        async load() {
            // if (!Editlist.editlist.transform_source) {
            //     try {
            //         Editlist.editlist.transform_source = await UGCRes.getTransformSource();
            //         window.transform_source =
            //             Editlist.editlist.transform_source;
            //     } catch (e) {
            //         //get error
            //         Log.error("get player transform glsl failed");
            //     }
            // }

            FFPlayInstance = this;
            this.throttle_ing = this.seeking = false;
            if (this.postermode)
                await this.$refs.video.load(Editlist.getPosterPlayerParam());
            else await this.$refs.video.load(Editlist.getPlayerParam());

            if (!Editlist.editlist.poster.url) await this.makeThumb();
        },
    },
};
</script>