import { Const } from "jsugc-websdk";
let i;
let ret = {
    CaptureVideo: {
        Duration: 10 * 60, //seconds
    },
    Operate: {
        Init: Operate("", (i = 0)),
        VideoAdd: Operate("", ++i),
        VideoSplit: Operate("", ++i),
        VideoTimeModify: Operate("", ++i),
        VideoRemove: Operate("", ++i),
        VideoParamsModify: Operate("", ++i),
        VideoSort: Operate("", ++i),
        VideoRotate: Operate("", ++i),
        VideoVFlip: Operate("", ++i),
        VideoHFlip: Operate("", ++i),
        VideoAspect: Operate("", ++i),
        PosterModify: Operate("", ++i),
        EndingModify: Operate("", ++i),
        ImageAdd: Operate("", ++i),
        ImageRemove: Operate("", ++i),
        ImageModify: Operate("", ++i),
        ImageTimeModify: Operate("", ++i),
        MosicAdd: Operate("", ++i),
        MosicRemove: Operate("", ++i),
        MosicModify: Operate("", ++i),
        MosicTimeModify: Operate("", ++i),
        TextAdd: Operate("", ++i),
        TextRemove: Operate("", ++i),
        TextModify: Operate("", ++i),
        TextTimeModify: Operate("", ++i),
        FilterEnable: Operate("", ++i),
        FilterDisable: Operate("", ++i),
        FilterModify: Operate("", ++i),
        FaceEnable: Operate("", ++i),
        FaceDisable: Operate("", ++i),
        FaceModify: Operate("", ++i),
        BGMSet: Operate("", ++i),
        BGMUnset: Operate("", ++i),
        BGMParamsModify: Operate("", ++i),
        AudioParamsModify: Operate("", ++i),
        AudioEffectAdd: Operate("", ++i),
        AudioEffectRemove: Operate("", ++i),
        AudioEffectModify: Operate("", ++i),
        AudioEffectTimeModify: Operate("", ++i),
        AutoModify: Operate("", ++i),
    },
    ...Const,
};

function Operate(Name, Type, Reload) {
    return {
        Name,
        Type,
        Reload,
    };
}

export default ret;
