<template>
    <div class="audio-base-main" @touchend="touchend">
        <span style="color:white;width:100px">({{volume}})</span>
        <mu-slider style="margin:0" :min="0" :max="300" :step="1" v-model="volume" />
    </div>
</template>
<style scoped>
.audio-base-main {
    display: flex;
}
</style>
<script>
import { Editlist, Log } from "jsugc-websdk";
import Const from "@/const";
import throttle from "lodash.throttle";
import OpHistory from "@/model/History";
export default {
    props: ["title"],
    data() {
        return {
            volume: 100,
        };
    },
    watch: {
        volume(v) {
            this.changeVolume(v);
        },
    },
    methods: {
        changeVolume: throttle((v) => {
            Editlist.editlist.audio.volume = v;
            Log.uilog("/tool/volume", `changed: ${v}`);
        }, 500),
        touchend() {
            // this.$emit('change')
        },
        onDialogClose() {
            if (this.initState) {
                if (this.initState.volume != this.audio.volume) {
                    OpHistory.createOperateHistory(
                        Const.Operate.AudioParamsModify.Type
                    );
                }
            }
        },
    },
    mounted() {
        this.volume = Editlist.editlist.audio.volume;
        this.$emit("update:title", "");
        this.initState = {
            volume: this.audio.volume,
        };
    },
};
</script>