<template>
    <div class="main">
        <div class="main-maker" v-show="!makePoster&&!makeEnding&&!doExport">
            <ClipTop
                ref="top"
                :overrideBackbutton="!doExport&&!makePoster&&!makeEnding"
                @export="doExport = true"
            />
            <div ref="mainBox" class="main-box">
                <PreviewPlayer
                    ref="preview"
                    :pause="player.pause || doExport"
                    @finished="playerFinished"
                    @loading="player.loading=true"
                    @loaddone="previewLoaddone"
                    @resize="!makePoster&&!makeEnding&&!doExport"
                    @render="preview_render=true"
                    class="main-video"
                />

                <OverlayEditor
                    ref="overlayEditor"
                    @change="overlayEditorChange"
                    @overlayActive="overlayActive"
                    @overlayDeactive="overlayDeactive"
                    class="overlay-box"
                />
            </div>

            <div ref="toolBox" class="toolBox">
                <PlayerController
                    v-show="showPlayerController"
                    :playing="!player.pause"
                    :loading="player.loading"
                    @event="playerControllerEvent"
                />

                <ClipTimeline
                    ref="timeline"
                    v-show="showTimeline"
                    :show_overlay="showOverlay"
                    :overlay_type="overlayType"
                    :show_amix="showAMix"
                    :playing="!player.pause"
                    :poster="editlist.poster"
                    :preview_render="preview_render"
                    :player="$refs.preview"
                    @timelinesChange="timelinesChange"
                    @timeChange="timeChange"
                    @overlayChange="overlayChange"
                    @overlayActive="overlayActive"
                    @amixChange="amixChange"
                    @amixActive="amixActive"
                    @on_poster_click="makePoster=1"
                    @on_ending_click="makeEnding=1"
                />
                <ToolTab
                    ref="toolTab"
                    v-show="showTooltab"
                    :activeItem="activeItem"
                    @select="tabSelect"
                />
                <ToolDialog
                    @close="showTooltab=showPlayerController=showTimeline=true;mainBoxResized()"
                    @beforechange="toolBeforeChange"
                    @change="toolValueChange"
                    ref="toolDlg"
                />
            </div>

            <div v-if="importProgress >= 0" class="spin">
                <div>
                    <mu-circular-progress mode="determinate" :value="importProgress*100"></mu-circular-progress>
                    <br />
                    <span style="font-size:14px"> {{ (importProgress*100).toFixed(2) }}%</span>
                </div>
            </div>
        </div>
        <PosterMaker v-if="makePoster" @close="makePoster = false; reloadPlayer(true); " />
        <EndingMaker v-if="makeEnding" @close="makeEnding = false; reloadPlayer();" />
        <Exporter
            ref="exporter"
            v-if="doExport"
            @close="doExport = false; isBackbuttonExit = true; reloadPlayer();"
        />
        <Importer
            ref="importer"
            @select="importFileSelect"
            @progress="importProgress=$event"
            @cancel="importCancel"
        />

        <vue-snotify></vue-snotify>
    </div>
</template>

<style lang="less" scoped>
