    background-color: #333;
    background: transparent;
    position: relative;

    width: 100%;
    height: 100%;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.main-maker {
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    overflow: hidden;
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: column;
}
.main-box {
    background-color: #333;
    background: transparent;
    width: 100%;
    position: relative;
    overflow: hidden;
    flex: auto;
    display: flex;
    height: 100%;
}
