    background: black;
    position: absolute;
    display: flex;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    flex-flow: column;
    width: 100%;
    height: 100%;
}

.main-video {
    background-color: #000;
    background: transparent;
    width: 100%;
    /* display: flex; */
    /* height: 100%; */
}

