</style>
<style lang="less">
.snotifyToast {
    .snotifyToast__progressBar {
        height: 2px;
        overflow: hidden;
    }
    .snotifyToast__inner {
        min-height: 18px;
        .snotify-icon {
            max-height: 18px;
            max-width: 18px;
        }
    }
}
</style>
<script>
import Const from "@/const";
import extend from "extend";
import { roundFloat, rotateVector } from "@/utils";
import OpHistory from "@/model/History";
import ClipTop from "@/views/Top/ClipTop";
import ClipTimeline from "@/views/Bottom/Timeline/ClipTimeline";
import PlayerController from "@/views/Bottom/PlayerController";
import ToolTab from "@/views/Bottom/ToolTab";
import ToolDialog from "@/views/Bottom/ToolDialog";
import OverlayEditor from "@/views/Editor/OverlayEditor";
import PosterMaker from "@/views/PosterMaker";
import EndingMaker from "@/views/EndingMaker";
import Exporter from "@/views/Exporter";
import Importer from "@/views/Importer";
import ugcsdk, { Editlist, UGCResourceManager, Log } from "jsugc-websdk";
import CordovaModel from "jsugc-websdk/src/model/CordovaModel";

export default {
    components: {
        ClipTop,
        ClipTimeline,
        PlayerController,
        ToolTab,
        ToolDialog,
        OverlayEditor,
        PosterMaker,
        EndingMaker,
        Exporter,
        Importer,
    },
    data() {
        return {
            player: {
                pause: true,
                loading: false,
                going2play: false,
            },
            preview_render: false, //  mediacodec 
            editlist: Editlist.editlist,
            showOverlay: false, //  / 
            overlayType: false, // /  
            showAMix: false, //   
            showTooltab: true, // 
            showPlayerController: true,
            showTimeline: true,
            activeItem: null, // //
            makePoster: false, // 
            makeEnding: false, // 
            doExport: false, // 
            importProgress: -1,
        };
    },
    watch: {
        ["player.pause"](v) {
            if (!v) {
                this.$refs.overlayEditor.play();
            } else {
                this.$refs.overlayEditor.pause();
            }
        },
    },
    methods: {
        async reloadPlayer(dont_keep_screen) {
            this.player.pause = true;
            this.preview_render = false;
            await this.$refs.preview.reload(!dont_keep_screen);
        },
        seekPlayer(sec) {
            this.$refs.preview.seek(sec);
            this.$refs.overlayEditor.seek(sec);
        },
        playerFinished() {
            this.player.pause = true;
        },
        timeChange(e) {
            this.player.pause = true;
            this.seekPlayer(e.time_sec);
        },
        timelinesChange(timelines) {
            this.editlist.timelines = timelines;
            OpHistory.createOperateHistory(Const.Operate.VideoTimeModify.Type);
        },
        amixChange(o) {
            this.editlist.update_amix(o);
            OpHistory.createOperateHistory(
                Const.Operate.AudioEffectTimeModify.Type
            );
        },
        amixActive(o) {
            this.activeItem = o;
        },
        overlayChange(o) {
            this.$refs.overlayEditor.active(o);

            this.editlist.update_overlay(o);

            if (o.type == Const.Overlay.Type.Image) {
                OpHistory.createOperateHistory(
                    Const.Operate.ImageTimeModify.Type
                );
            } else if (o.type == Const.Overlay.Type.Text) {
                OpHistory.createOperateHistory(
                    Const.Operate.TextTimeModify.Type
                );
            } else if (o.type == Const.Overlay.Type.Mosic) {
                OpHistory.createOperateHistory(
                    Const.Operate.MosicTimeModify.Type
                );
            }
        },
        overlayActive(o) {
            if (o) {
                this.$refs.overlayEditor.active(o);
            } else {
                this.$refs.overlayEditor.blur();
                if (this.activeItem && this.activeItem.active) {
                    this.activeItem.active = false;
                }
            }
            this.activeItem = o;
        },
        overlayDeactive() {
            this.activeItem = null;
        },
        blankClicked() {
            this.$refs.overlayEditor.blur();
        },
        previewLoaddone() {
            this.player.loading = false;
            this.player.pause = !this.player.going2play;
            this.player.going2play = false;
        },
        async playerControllerEvent(e) {
            let cur_timeline;
            let time_sec;

            let eventname = "";
            try {
                eventname = Object.keys(PlayerController.EventID)[
                    Object.values(PlayerController.EventID).indexOf(e.eventID)
                ];
            } catch (e) {
                //
            }
            Log.uilog("/", `player control: ${eventname}`);

            switch (e.eventID) {
                case PlayerController.EventID.Play:
                    if (
                        !this.$refs.preview.$refs.video.seeking &&
                        this.$refs.preview.$refs.video.loaded
                    )
                        this.player.pause = false;
                    else {
                        this.player.going2play = true;
                    }
                    break;
                case PlayerController.EventID.Pause:
                    this.player.pause = true;
                    break;
                case PlayerController.EventID.SeekBegin:
                    this.player.pause = true;
                    cur_timeline = this.editlist.cur_timeline();
                    time_sec = roundFloat(this.editlist.time_sec);
                    if (cur_timeline) {
                        if (
                            Math.abs(time_sec - cur_timeline.offset_sec) < 0.05
                        ) {
                            // 
                            let prevIdx = Math.max(
                                0,
                                this.editlist.timelines.findIndex(
                                    (e) => e.id == cur_timeline.id
                                ) - 1
                            );
                            cur_timeline = this.editlist.timelines[prevIdx];
                        }

                        //  time_sec
                        await new Promise((o) => setTimeout(o, 100));

                        time_sec = cur_timeline.offset_sec + 0.01;
                        this.seekPlayer(time_sec);
                        this.$refs.timeline.update_time_sec(time_sec);
                    }
                    break;
                case PlayerController.EventID.SeekEnd:
                    this.player.pause = true;
                    cur_timeline = this.editlist.cur_timeline();
                    time_sec = roundFloat(this.editlist.time_sec);
                    if (cur_timeline) {
                        if (
                            time_sec ==
                            roundFloat(
                                cur_timeline.offset_sec +
                                    cur_timeline.end_sec -
                                    cur_timeline.start_sec
                            )
                        ) {
                            // 
                            let nextIdx =
                                this.editlist.timelines.indexOf(cur_timeline) +
                                1;
                            if (nextIdx < this.editlist.timelines.length) {
                                cur_timeline = this.editlist.timelines[nextIdx];
                            }
                            // 0.001
                            // this.player.time_sec = cur_timeline.offset_sec + cur_timeline.start_sec + 0.001;
                        }

                        //  time_sec
                        await new Promise((o) => setTimeout(o, 100));

                        // 
                        time_sec =
                            cur_timeline.offset_sec +
                            cur_timeline.end_sec -
                            cur_timeline.start_sec +
                            0.01;
                        this.seekPlayer(time_sec);
                        this.$refs.timeline.update_time_sec(time_sec);
                    }
                    break;
                case PlayerController.EventID.Undo:
                    this.undo();
                    break;
                case PlayerController.EventID.AspectRatio:
                    this.$refs.toolDlg.show(ToolTab.TabID.AspectRatio, {
                        height: this.$refs.toolTab.$el.clientHeight + "px",
                    });
                    break;
            }
        },
        toolBeforeChange() {
            // 
            this.player.pause = true;
        },
        toolValueChange(data, tabid) {
            // 
            if (tabid == ToolTab.TabID.AspectRatio) {
                this.player.pause = true;
            }

            if (data) {
                if (data.overlay) {
                    this.overlayActive(data.overlay);

                    if (data.overlay.type == Const.Overlay.Type.Text) {
                        this.reloadPlayer();
                    }
                    // this.$refs.overlayEditor.active(data.overlay);
                    // this.$refs.overlayToolTab.active(data.overlay);
                    return;
                }
            }
            this.reloadPlayer();
        },
        overlayEditorChange() {
            this.reloadPlayer();
        },
        tabSelect(tabID) {
            let tabname = "";
            try {
                tabname = Object.keys(ToolTab.TabID)[
                    Object.values(ToolTab.TabID).indexOf(tabID)
                ];
            } catch (e) {
                //
            }
            Log.uilog("/", `tab change: ${tabname}`);
            switch (tabID) {
                case ToolTab.TabID.ImageTab:
                    this.showOverlay = true;
                    this.overlayType =
                        Const.Overlay.Type.Image | Const.Overlay.Type.Mosic;
                    this.showAMix = false;
                    break;
                case ToolTab.TabID.TextTab:
                    this.showOverlay = true;
                    this.overlayType = Const.Overlay.Type.Text;
                    this.showAMix = false;
                    break;
                case ToolTab.TabID.AudioTab:
                    this.showAMix = true;
                    this.showOverlay = false;
                    break;
                case ToolTab.TabID.OtherTab:
                    // //  
                    // this.showOverlay = true;
                    // // this.overlayType = Const.Overlay.Type.Mosic;
                    // this.showAMix = false;

                    // 
                    this.showOverlay = false;
                    this.showAMix = false;
                    break;

                case ToolTab.TabID.ToolEdit:
                case ToolTab.TabID.ToolTemplate:
                case ToolTab.TabID.ToolSettings:
                    if (this.activeItem.type == Const.Overlay.Type.Text) {
                        this.showTimeline = false;
                        this.showPlayerController = false;

                        this.$refs.toolDlg.show(ToolTab.TabID.AddText, {
                            height: "auto",
                            overlay: this.activeItem,
                            operateToolTabId: tabID,
                        });
                    }
                    break;
                case ToolTab.TabID.ToolDone:
                    this.activeItem.active = false;
                    this.activeItem = null;
                    this.$refs.overlayEditor.blur();
                    break;
                case ToolTab.TabID.ToolRemove:
                    this.editlist.remove_overlay(this.activeItem.id);
                    this.activeItem.active = false;

                    switch(this.activeItem.type){
                        case Const.Overlay.Type.Image:
                            OpHistory.createOperateHistory(
                                Const.Operate.ImageRemove.Type
                            );
                            break;
                        case Const.Overlay.Type.Text:
                            OpHistory.createOperateHistory(
                                Const.Operate.TextRemove.Type
                            );
                            break;
                        case Const.Overlay.Type.Mosic:
                            OpHistory.createOperateHistory(
                                Const.Operate.MosicRemove.Type
                            );
                            break;
                        default:
                            OpHistory.createOperateHistory(
                                Const.Operate.AudioEffectRemove.Type
                            );
                            break;
                    }

                    this.$refs.overlayEditor.blur();
                    this.activeItem = null;
                    this.reloadPlayer();

                    break;

                case ToolTab.TabID.VideoAdd:
                    this.pickVideo();
                    break;
                case ToolTab.TabID.VideoRemove:
                    {
                        let timelines = this.editlist.timelines;

                        // 
                        let idx = timelines.findIndex((o) => o.active);
                        if (idx == -1) {
                            // 
                            let curr_timeline = this.editlist.cur_timeline();
                            if (!curr_timeline && timelines.length) {
                                // 
                                idx = 0;
                            } else {
                                idx = timelines.findIndex(
                                    (e) => e.id == curr_timeline.id
                                );
                            }
                        }
                        if (idx >= 0) {
                            let timeline = timelines[idx];
                            timelines.splice(idx, 1);
                            [0, ...timelines].reduce((pre, nxt) => {
                                nxt.offset_sec = pre;
                                return (
                                    nxt.offset_sec + nxt.end_sec - nxt.start_sec
                                );
                            });

                            // 
                            [
                                ...this.editlist.amix,
                                ...this.editlist.overlay,
                            ].forEach((e) => {
                                let found =
                                    roundFloat(timeline.offset_sec) <=
                                        roundFloat(e.offset_sec) &&
                                    roundFloat(e.offset_sec) <
                                        roundFloat(
                                            timeline.offset_sec +
                                                timeline.end_sec -
                                                timeline.start_sec
                                        );
                                if (found) {
                                    this.editlist.remove_overlay(e.id);
                                }
                            });

                            this.player.pause = true;

                            let time_sec = 0;
                            // 
                            if (idx == 0 || !timelines.length) {
                                time_sec = 0;
                            } else {
                                time_sec =
                                    timelines[idx - 1].offset_sec +
                                    timelines[idx - 1].end_sec -
                                    timelines[idx - 1].start_sec +
                                    0.01;
                            }

                            this.seekPlayer(time_sec);
                            this.$refs.timeline.update_time_sec(time_sec);
                            this.reloadPlayer();

                            OpHistory.createOperateHistory(
                                Const.Operate.VideoRemove.Type,
                                timeline.id
                            );
                        }

                        if (timelines.length == 0) {
                            this.editlist.poster.url = "";
                            this.editlist.poster.overlay.splice(0, -1);
                            this.pickVideo();
                        }
                    }
                    break;
                case ToolTab.TabID.VideoSplit:
                    {
                        this.player.pause = true;

                        let timelines = this.editlist.timelines;
                        let time_sec =
                            (+this.editlist.time_sec).toFixed(3) - 0.01;
                        let found = this.editlist.cur_timeline();
                        let idx = timelines.findIndex((o) => o.id == found.id);

                        let second = extend(true, {}, found, {
                            id: this.editlist.get_next_timelineId(),
                        });

                        found.active = false;

                        if (
                            time_sec - found.offset_sec <
                                this.editlist.video_min_sec ||
                            found.offset_sec +
                                found.end_sec -
                                found.start_sec -
                                time_sec <
                                this.editlist.video_min_sec
                        ) {
                            this.$snotify.error(
                                `${this.editlist.video_min_sec}`
                            );
                            return;
                        }

                        found.end_sec = second.start_sec =
                            time_sec - found.offset_sec + found.start_sec;

                        timelines.splice(idx + 1, 0, second);
                        timelines.reduce((pre, nxt, idx) => {
                            if (idx == 1) pre = pre.end_sec - pre.start_sec;
                            nxt.offset_sec = pre;
                            return nxt.offset_sec + nxt.end_sec - nxt.start_sec;
                        });

                        this.editlist.cur_timeline(true);
                        this.$refs.timeline.refreshUI();
                        this.reloadPlayer();

                        OpHistory.createOperateHistory(
                            Const.Operate.VideoSplit.Type
                        );
                    }
                    break;
                case ToolTab.TabID.VideoRotate:
                    {
                        let timelines = this.editlist.timelines;

                        let found = this.editlist.cur_timeline();
                        let idx = timelines.findIndex((o) => o.id == found.id);

                        if (idx >= 0) {
                            let found = timelines[idx];

                            if (idx == 0) {
                                // 
                                this.editlist.poster.rotate += 270;
                                this.editlist.poster.rotate %= 360;
                                this.rotatePosterOverlay(found, 270);
                            }

                            found.rotate += 270;
                            found.rotate %= 360;

                            this.reloadPlayer();

                            OpHistory.createOperateHistory(
                                Const.Operate.VideoRotate.Type
                            );
                        }
                    }
                    break;
                case ToolTab.TabID.VideoVFlip:
                    {
                        let timelines = this.editlist.timelines;
                        let idx = -1;
                        timelines.forEach((o, i) => {
                            if (
                                o.offset_sec < this.editlist.time_sec &&
                                o.end_sec - o.start_sec + o.offset_sec >
                                    this.editlist.time_sec
                            )
                                idx = i;
                        });
                        if (idx >= 0) {
                            let found = timelines[idx];
                            found.vflip = !found.vflip;
                            this.reloadPlayer();

                            OpHistory.createOperateHistory(
                                Const.Operate.VideoVFlip.Type
                            );
                        }
                    }
                    break;
                case ToolTab.TabID.VideoHFlip:
                    {
                        let timelines = this.editlist.timelines;
                        let idx = -1;
                        timelines.forEach((o, i) => {
                            if (
                                o.offset_sec < this.editlist.time_sec &&
                                o.end_sec - o.start_sec + o.offset_sec >
                                    this.editlist.time_sec
                            )
                                idx = i;
                        });
                        if (idx >= 0) {
                            let found = timelines[idx];
                            found.hflip = !found.hflip;
                            this.reloadPlayer();

                            OpHistory.createOperateHistory(
                                Const.Operate.VideoHFlip.Type
                            );
                        }
                    }
                    break;
                case ToolTab.TabID.AddLocalImage:
                    this.pickImg();
                    break;
                case ToolTab.TabID.Mosic:
                    this.addMosic();
                    break;
                default: {
                    let height;

                    switch (tabID) {
                        case ToolTab.TabID.Face:
                        case ToolTab.TabID.AudioBase:
                            this.player.pause = true;
                            height = this.$refs.toolTab.$el.clientHeight + "px";
                            break;
                        case ToolTab.TabID.AddText:
                            this.showTimeline = false;
                            this.showPlayerController = false;
                            height = "auto";
                            break;
                        case ToolTab.TabID.BGM:
                            height = this.$el.clientHeight / 2 + "px";
                            break;
                        default:
                            height = this.$refs.toolBox.clientHeight + "px";
                    }

                    this.$refs.toolDlg.show(tabID, {
                        height,
                    });
                }
            }

            if (
                tabID == ToolTab.TabID.ImageTab ||
                tabID == ToolTab.TabID.TextTab ||
                tabID == ToolTab.TabID.AudioTab ||
                tabID == ToolTab.TabID.OtherTab
            ) {
                this.overlayActive(null);
            }
        },
        async pickImg() {
            Log.uilog("/", "pick image");
            // native cordova.camera plugin  content:// 
            // CameraLauncher.java::processResultFromGallery::707  fileLocation
            let file = await new Promise((o, x) =>
                navigator.camera.getPicture(o, x, {
                    sourceType: Camera.PictureSourceType.PHOTOLIBRARY,
                    allowEdit: false,
                    correctOrientation: true,
                    mediaType: Camera.MediaType.PICTURE,
                })
            );

            // window.console.log(file);
            if (!file) return;

            let overlay = this.editlist.add_overlay({
                url: file,
                type: Const.Overlay.Type.Image,
            });
            this.$refs.overlayEditor.active(overlay);
            this.overlayActive(overlay);

            Log.uilog("/", `pick image done, url: ${overlay.url}`);

            OpHistory.createOperateHistory(Const.Operate.ImageAdd.Type);
        },
        async importFileSelect(files, auto) {
            // try {
            let needSeek = this.editlist.timelines.length;
            let time_sec = this.editlist.total_sec;
            let timelines = await Promise.all(
                files.map((val) => {
                    let src = val.uri.replace("file://", "");
                    return {
                        url: src,
                    };
                })
            );

            Log.uilog("/", `file imported: ${JSON.stringify(timelines)}`);

            try {
                await this.editlist.add_timelines(timelines);
            } catch (e) {
                Log.error("add timeline failed", e);
                this.$snotify.error(e);
                return;
            }

            if (!auto) {
                // reload
                timelines.forEach((t) => {
                    OpHistory.createOperateHistory(
                        Const.Operate.VideoAdd.Type,
                        t.id
                    );
                });
            }

            await this.$refs.preview.reload();

            time_sec += 0.01;
            if(needSeek){
                this.seekPlayer(time_sec);
                this.$refs.timeline.update_time_sec(time_sec);
            }

            Log.uilog("/", `player loaded`);

            this.importProgress = -1;

            //  
            if (!this.editlist.timelines.length) {
                this.pickVideo();
            }
        },
        importCancel(){
            if(this.editlist.timelines.length <= 0){
                this.$refs.top.exit(true);
            }
        },
        async pickVideo() {
            this.$refs.importer.open();
        },
        mainBoxResized() {
            this.$nextTick(() => {
                this.$refs.overlayEditor.updateUI();
            });
        },
        async addMosic() {
            Log.uilog("/", "add mosic");
            if (!this.editlist.mosic_source) {
                this.editlist.mosic_source = await UGCResourceManager.getMosicSource();
            }
            let mosic = this.editlist.add_mosic();

            this.activeItem = mosic;
            this.$refs.overlayEditor.active(mosic);
            this.$refs.overlayEditor.toCenter(1);

            await this.reloadPlayer();

            Log.uilog("/", "add mosic done");

            OpHistory.createOperateHistory(
                Const.Operate.MosicAdd.Type,
                mosic.id
            );
        },
        async undo() {
            this.player.pause = true;

            let message = await OpHistory.undo(this.$refs.preview);

            Log.uilog("/", `history undo: ${message}`);

            this.$refs.overlayEditor.blur();
            if (this.activeItem) {
                this.activeItem.active = false;
            }

            this.$snotify.success(message);

            // 
            if (!Editlist.editlist.timelines.length) {
                this.pickVideo();
            }
        },
        // overlay
        rotatePosterOverlay(timeline, rotate) {
            // overlay
            let width = this.editlist.getRotatedWidth(timeline);
            let ox = width;
            let oy = 0;

            this.editlist.poster.overlay.forEach((overlay) => {
                // 
                let box = Math.hypot(overlay.w, overlay.h);
                let x = overlay.x - (overlay.w - box) / 2;
                let y = overlay.y - (overlay.h - box) / 2;

                let rotatedLT = rotateVector(ox, oy, x, y, rotate);
                let rotatedRB = rotateVector(
                    ox,
                    oy,
                    x + overlay.w,
                    y + overlay.h,
                    rotate
                );
                // 
                ox = (rotatedLT.x + rotatedRB.x) / 2;
                oy = (rotatedLT.y + rotatedRB.y) / 2;
                rotatedLT = rotateVector(
                    ox,
                    oy,
                    rotatedLT.x,
                    rotatedLT.y,
                    -rotate
                );
                // 
                rotatedLT.x -= width;

                overlay.x = rotatedLT.x + (overlay.w - box) / 2;
                overlay.y = rotatedLT.y + (overlay.h - box) / 2;
                overlay.rotate += rotate;
                overlay.rotate %= 360;
            });
        },
    },
    async mounted() {
        Log.info(`JSUGCSDK verison: ${ugcsdk.version}`);

        Log.uilog("/", "begin");

        if (!this.isCordova) {
            // this.$Message.error("UGC");
            // this.$router.back();
            Log.warn("UGC");
            return;
        }

        let config = await CordovaModel.getConfig();

        if (config.input) {
            Log.uilog("/", `config.input video: ${config.input}`);
            // SDK
            await this.importFileSelect(
                [
                    {
                        uri: config.input,
                    },
                ],
                true
            );
        } else {
            this.pickVideo();
        }

        OpHistory.createOperateHistory(Const.Operate.Init.Type);
    },
    async beforeDestroy() {
        // document.removeEventListener("backbutton", this.onBack, false);
    },
};
</script>
