// isCordova = isCordova || true
import UGCRes from "./UGCResourceManager";
import Log from "./Log";
let portraitListener = null;
let portraitQuery = window.matchMedia("(orientation: portrait)");
let isCordova = navigator.userAgent.indexOf("(cordova)") >= 0;
export default {
    isCordova() {
        return isCordova;
    },
    isIOS() {
        return navigator.userAgent.toLowerCase().indexOf("iphone") >= 0;
    },
    isAndroid() {
        return navigator.userAgent.toLowerCase().indexOf("android") >= 0;
    },
    isPortrait(onchange) {
        if (onchange) {
            portraitListener && portraitQuery.removeListener(portraitListener);
            portraitQuery.addListener((portraitListener = onchange));
        }
        return portraitQuery.matches;
    },
    config: {
        noReady: true,
        input: "",
        output: "",
        output_thumb: "",
    },
    async getConfig() {
        if (this.config.noReady) {
            this.config = await new Promise(UGCApp.getConfig);
            Log.verbose("config: ", this.config);
        }
        return this.config;
    },
    setConfig(config) {
        this.config = config;
    },
    StatusCode: {
        // from init()
    },
    async finish(result) {
        await UGCRes.clearTempFiles();
        await UGCApp.ffplay.stop(false);
        await new Promise(UGCApp.done.bind(UGCApp, result));
    },
    async init() {
        if (!this.isCordova() || window.Cordova) return;

        let sysFile = window.File;
        await new Promise((o, x) => {
            let s = document.createElement("script");
            if (this.isAndroid()) s.src = "cordova/android/cordova.js";
            else if (this.isIOS()) s.src = "cordova/ios/cordova.js";
            s.onload = () => {
                document.addEventListener("deviceready", o);
            };
            s.onerror = x;
            document.head.appendChild(s);
        });
        // cordova-plugin-file  window.File (stream-v1.js)
        window.File = sysFile;

        this.StatusCode = UGCApp.StatusCode;
        let loglevel = (await this.getConfig()).loglevel;
        if(loglevel !== undefined && loglevel > 0)
            Log.loglevel = loglevel;
        Log.info("cordova init finished");
    },
    async getCdvUrl(i) {
        if (i.indexOf("http://localhost") == 0) {
            i = i.replace(/http:\/\/localhost:.*?\//, "cdvfile://localhost/");
        }
        if (i.indexOf("/") == 0) {
            i = "file://" + i;
        }
        return await new Promise((o, x) =>
            resolveLocalFileSystemURL(
                i,
                function(entry) {
                    o(entry.toInternalURL());
                },
                () => {
                    x();
                }
            )
        );
    },
};
