import { Editlist, Log } from "jsugc-websdk";
import extend from "extend";
import Const from "@/const";
export default {
    // 
    operateHistory: [],
    async undo(playerRef) {
        let his = this.undoOperateHistory();
        if (his) {
            let undoOp = his.undo; // 
            let restoreOp = his.restore; // 

            let restoreArray = (target, src) => {
                target.splice(0, target.length);
                src.forEach((e) => target.push(extend(true, {}, e)));
            };

            restoreArray(Editlist.editlist.timelines, restoreOp.timelines);
            restoreArray(Editlist.editlist.amix, restoreOp.amix);
            restoreArray(Editlist.editlist.overlay, restoreOp.overlay);
            restoreArray(Editlist.editlist.mosic, restoreOp.mosic);
            restoreArray(Editlist.editlist.filter, restoreOp.filter);

            Editlist.editlist.face = restoreOp.face;
            Editlist.editlist.video = restoreOp.video;
            Editlist.editlist.audio = restoreOp.audio;
            Editlist.editlist.bgm = restoreOp.bgm;
            Editlist.editlist.poster = restoreOp.poster;

            let undoOpKey = Object.keys(Const.Operate).find(
                (k) => Const.Operate[k].Type == undoOp.operateType
            );

            let target;
            switch (undoOp.operateType) {
                case Const.Operate.VideoRemove.Type:
                    target = Editlist.editlist.timelines.find(
                        (e) => undoOp.targetId == e.id
                    );
                    target.active = true;
                    playerRef.seek(target.offset_sec);
                    break;
            }

            if (Editlist.editlist.time_sec > Editlist.editlist.total_sec) {
                playerRef.seek(Editlist.editlist.total_sec);
            }

            // playerRef.pause = true;
            // this.$refs.overlayEditor.blur();
            // if (this.activeItem) {
            //     this.activeItem.active = false;
            // }

            playerRef.reload();

            return " " + Const.Operate[undoOpKey].Name;
            // this.$mResToast.success(
            //     " " + Const.Operate[undoOpKey].Name
            // );
            // window.console.log("undo: ", restoreOp);
        } else {
            // this.$mResToast.success("");
            return "";
        }
    },
    undoOperateHistory() {
        let ops = this.getOperateHistory();
        if (ops && ops.length > 1) {
            let undo = ops.pop();

            this.saveOperateHistory(ops);

            return {
                restore: extend(true, {}, ops[ops.length - 1]),
                undo: extend(true, {}, undo),
                history: ops,
            };
        }
    },
    getOperateHistory() {
        if (!this.operateHistory) {
            try {
                // return this.operateHistory = JSON.parse(sessionStorage.getItem("operate-history"));
            } catch (error) {
                //
            }
        }
        return this.operateHistory || [];
    },
    createOperateHistory(operateType, targetId) {
        let editlist = Editlist.editlist;
        let op = extend(
            true,
            {},
            {
                operateType: operateType,
                targetId: targetId,
                timelines: editlist.timelines,
                amix: editlist.amix,
                overlay: editlist.overlay,
                mosic: editlist.mosic,
                filter: editlist.filter,
                face: editlist.face,
                video: editlist.video,
                audio: editlist.audio,
                bgm: editlist.bgm,
                poster: editlist.poster,
            }
        );
        // op.timelines.forEach(e => e.thumbs = []);
        op.amix.forEach((e) => (e.active = false));
        op.overlay.forEach((e) => {
            e.active = false;
            e.enabled = true;
        });
        op.mosic.forEach((e) => {
            e.active = false;
            e.enabled = true;
        });

        Log.verbose("create operate history: ", op);
        return this.pushOperateHistory(op);
    },
    // 1.ClipImg
    // 
    // 2.TimelineItemThumb
    // 
    // target: timeline
    updateOperateHistory(operateType, target) {
        let ops = this.getOperateHistory();
        let lastOp = ops[ops.length - 1];
        if (!lastOp) {
            return ops;
        }

        if (lastOp.operateType == operateType) {
            if (operateType == Const.Operate.MosicAdd.Type) {
                let oldMosic = lastOp.mosic.find((e) => e.id == target.id);
                if (oldMosic) {
                    extend(oldMosic, target);
                    oldMosic.active = false;
                    oldMosic.enabled = true;
                }
            } else if (operateType == Const.Operate.ImageAdd.Type) {
                let oldOverlay = lastOp.overlay.find(
                    (e) =>
                        e.type == Const.Overlay.Type.Image && e.id == target.id
                );
                if (oldOverlay) {
                    extend(oldOverlay, target);
                    oldOverlay.active = false;
                    oldOverlay.enabled = true;
                }
            } else if (operateType == Const.Operate.TextAdd.Type) {
                let oldOverlay = lastOp.overlay.find(
                    (e) =>
                        e.type == Const.Overlay.Type.Text && e.id == target.id
                );
                if (oldOverlay) {
                    extend(oldOverlay, target);
                    oldOverlay.active = false;
                    oldOverlay.enabled = true;
                }
            }
        }
        // Const.Operate.Init.Type
        if (operateType == Const.Operate.VideoAdd.Type) {
            if (
                lastOp.operateType == operateType ||
                lastOp.operateType == Const.Operate.Init.Type
            ) {
                let oldTimeline = lastOp.timelines.find(
                    (e) => e.id == target.id
                );
                if (oldTimeline) {
                    oldTimeline.thumbs = [];
                    extend(oldTimeline, target);
                }
            }
        }

        Log.verbose("update operate history: ", lastOp);
        return this.saveOperateHistory(ops);
    },
    pushOperateHistory(data) {
        let ops = this.getOperateHistory();
        ops.push(data);
        if (ops.length > 200) {
            ops.splice(0, 1);
        }
        return this.saveOperateHistory(ops);
    },
    saveOperateHistory(ops) {
        try {
            this.operateHistory = ops;
            // sessionStorage.setItem("operate-history", JSON.stringify(ops));
            // 
        } catch (error) {
            //
        }
        return ops;
    },
};
