<template>
    <div class="box">
        <img
            v-for="(i,idx) in thumbs"
            :key="idx"
            :src="i.url || firstThumb"
            :style="{left:i.ss*pps+'px', width:i.duration*pps+'px'}"
            @error="thumbLoadError(idx)"
        />
    </div>
</template>
<style scoped>
.box {
    position: relative;
}
.box img {
    position: absolute;
}
</style>
<script>
const DURATION = 10;
const SPAN = 1;

import { Editlist } from "jsugc-websdk";
import { CordovaModel } from "jsugc-websdk";
import { UGCResourceManager as UGCRes } from "jsugc-websdk";
import { Log } from "jsugc-websdk";
import { Export } from "jsugc-websdk";
import Const from "@/const";
import { roundFloat } from "@/utils";
import OpHistory from "@/model/History";

let thumbsFolders = {}; // 

export default {
    props: ["idx", "pps", "timeline", "rotate", "canload"],
    data() {
        return {
            editlist: Editlist.editlist,
            thumbs: [],
        };
    },
    watch: {
        "editlist.time_sec": "update",
        "timeline.start_sec": "update",
        "timeline.end_sec": "update",
        async rotate() {
            // 
            // this.thumbs = [];
            // await this.makeAllThumb();
            // this.update();
        },
        canload(v) {
            //   mediacodec 
            if (v) this.fetchThumb(this.editlist.time_sec);
        },
    },
    computed: {
        firstThumb() {
            let thumb = this.thumbs.find((e) => e.url);
            return thumb && thumb.url;
        },
    },
    methods: {
        update() {
            let pts = Editlist.editlist.time_sec;
            pts -= this.timeline.offset_sec;

            if (
                pts < -DURATION ||
                pts > this.timeline.end_sec - this.timeline.start_sec
            )
                return;
            this.fetchThumb(pts);
            this.fetchThumb(pts - DURATION);
        },
        thumbLoadError(index) {
            if (this.thumbs[index] && this.thumbs[index].url) {
                this.thumbs.splice(index, 1);
                this.update();
            }
        },
        async prepareCacheFolder() {
            let fileName = "" + this.timeline.id;
            if (!thumbsFolders[fileName]) {
                let fs = await UGCRes.filesystem();

                //getDirectory  exclusive

                let thumbs = await new Promise(
                    fs.root.getDirectory.bind(fs.root, "thumbs/", {
                        create: true,
                    })
                );

                thumbsFolders[fileName] = await new Promise(
                    thumbs.getDirectory.bind(thumbs, fileName, { create: true })
                );
            }
        },
        // async makeThumb(){},
        async makeThumb(src, des, ss, duration, span) {
            Log.verbose("makeThumb:" + des);

            await this.prepareCacheFolder();

            // TODO 
            return await Export.Transcode.thumb(src, des, ss, duration, span);
        },
        async fetchThumb(time) {
            let pts = roundFloat(Math.max(0, time + this.timeline.start_sec));

            if (pts > roundFloat(this.timeline.end_sec)) {
                return;
            }

            let thumb = this.thumbs.find(
                (i) =>
                    roundFloat(i.ss) <= roundFloat(pts) &&
                    roundFloat(i.ss + i.duration) >= roundFloat(pts)
            );

            // window.console.log("fetch thumb:" + pts)

            if (!thumb || thumb.preload) {
                let ss = pts - (pts % DURATION);
                let duration = DURATION;
                let span = SPAN;
                let timeline = this.timeline;

                let src = timeline.url;
                let des = "{path}/thumbs/{id}/thumb_{ss}_{rn}.jpg"
                    .replace(
                        "{path}",
                        cordova.file.tempDirectory ||
                            cordova.file.cacheDirectory
                    )
                    .replace("{id}", timeline.id)
                    .replace("{ss}", ss)
                    // 
                    .replace(
                        "{rn}",
                        Math.random().toString(16).replace("0.", "")
                    );

                //

                let loaded = thumb;
                if (loaded) {
                    loaded.loading = true;
                    delete loaded.preload;
                } else {
                    loaded = {
                        loading: true,
                        url: "",
                        ss,
                        duration,
                    };
                    this.thumbs.push(loaded);
                }
                this.timeline.thumbs = this.thumbs;

                let ret = await this.makeThumb(src, des, ss, duration, span);
                if (ret) window.console.error("make thumb error:" + ret);

                delete loaded.loading;
                loaded.url = await CordovaModel.getCdvUrl(des);
                Log.verbose("make thumb:" + loaded.url);

                if (time == 0) {
                    OpHistory.updateOperateHistory(
                        Const.Operate.VideoAdd.Type,
                        timeline
                    );
                }
            }
        },
        makeAllThumb() {
            // 
            // this.thumbs
            let pts = this.timeline.start_sec;
            let end = this.timeline.end_sec;
            while (pts <= end) {
                let ss = pts - (pts % DURATION);

                let preload = {
                    preload: true,
                    url: "",
                    ss,
                    duration: DURATION,
                };

                this.thumbs.push(preload);
                this.timeline.thumbs = this.thumbs;

                pts += DURATION;
            }
        },
    },
    async mounted() {
        this.thumbs = this.timeline.thumbs; //[...this.timeline.thumbs];
        if (!this.thumbs.length) this.makeAllThumb();
        //  mediacodec samsung s9+ 4kmediacodec decoder
        if (this.canload) await this.fetchThumb(0);
    },
};
</script>