<template>
    <div>
        <div class="videobase-item">
            <span>({{eq.brightness}})</span>
            <mu-slider @change="change" :min="-1" :max="1" :step="0.02" v-model="eq.brightness" />
        </div>
        <div class="videobase-item">
            <span>({{eq.contrast}})</span>
            <mu-slider @change="change" :min="0" :max="5" :step="0.05" v-model="eq.contrast" />
        </div>
        <div class="videobase-item">
            <span>({{eq.saturation}})</span>
            <mu-slider @change="change" :min="0" :max="3" :step="0.03" v-model="eq.saturation" />
        </div>
        <div class="videobase-item">
            <span>({{eq.gamma}})</span>
            <mu-slider @change="change" :min="0.1" :max="10" :step="0.1" v-model="eq.gamma" />
        </div>
        <div style="text-align:center">
            <mu-button @click="resumeNormal" small></mu-button>
        </div>
    </div>
</template>
<style scoped>
.videobase-item {
    display: flex;
    margin-bottom: 10px;
}
.videobase-item .mu-slider {
    margin: 0;
}
.videobase-item span {
    width: 150px;
    color: white;
}
</style>
<script>
import extend from "extend";
import { Editlist } from "jsugc-websdk";
import Const from "@/const";
import OpHistory from "@/model/History";
export default {
    data() {
        return {
            eq: {
                brightness: 0,
                contrast: 0,
                saturation: 0,
                gamma: 0
            }
        };
    },
    methods: {
        resumeNormal() {
            this.eq.brightness = 0;
            this.eq.contrast = 1;
            this.eq.saturation = 1;
            this.eq.gamma = 1;
            this.change();
        },
        change() {
            Editlist.editlist.update_timeline_videobase(
                Editlist.editlist.cur_timeline()
            );
        },
        onDialogClose() {
            if (this.initState) {
                if (
                    Object.keys(this.eq).find(
                        k => this.eq[k] != this.initState.eq[k]
                    )
                ) {
                    OpHistory.createOperateHistory(
                        Const.Operate.VideoParamsModify.Type
                    );
                }
            }
        }
    },
    mounted() {
        this.eq = Editlist.editlist.cur_timeline().eq;
        this.$emit("update:title", "");
        this.initState = {
            eq: extend(true, {}, this.eq)
        };
    }
};
</script>