/**
 * 10
 * 
 * @param {*} val
 */
export function roundFloat(val) {
    return +val.toFixed(10);
}

/**
 * ox, oy: 
 * sx, sy: 
 * degrees: 
 */
export function rotateVector(ox, oy, sx, sy, degrees) {
    sx -= ox;
    sy -= oy;

    let radians = (degrees * Math.PI) / 180;
    let x = sx * Math.cos(radians) - sy * Math.sin(radians);
    let y = sx * Math.sin(radians) + sy * Math.cos(radians);

    return {
        x: x + ox,
        y: y + oy,
    };
}
