<template>
    <div class="mask">
        <div class="loading">
            <mu-icon value="refresh" class="loading-icon" size="30"></mu-icon>
        </div>
        <canvas class="render" ref="canvas"></canvas>
    </div>
</template>
<style scoped>
.mask {
    position: absolute;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: flex-end;
    display: flex;
    background: rgba(0, 0, 0, 0.7);
}
.render {
    display: none;
    width: 320px;
    height: 320px;
}

.loading {
    position: absolute;
    left: 50%;
    top: 50%;
    color: #fff;
    text-shadow: 0 0 30px #000;
    transform: translate(-50%, -50%);
}
.loading-icon {
    animation: loading 0.3s infinite linear;
    opacity: 0.9;
    vertical-align: -8px;
}

@keyframes loading {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
</style>
<script>
import { CordovaModel } from "jsugc-websdk";
import { UGCResourceManager as UGCResource } from "jsugc-websdk";

let DefaultSize = 1000;
let DefaultFontSize = 60;
let DefaultFont = "";

export default {
    props: ["avatarFile", "nickName", "desc"],
    methods: {
        async render() {
            let avatarSize = 5 * DefaultFontSize;
            let avatarOpt = {
                marginTop: -DefaultFontSize * 1.5 - avatarSize / 2,
                size: avatarSize,
                file: this.avatarFile
            };
            let nickNameOpt = {
                marginTop: 0,
                fontSize: DefaultFontSize,
                color: "white",
                font: DefaultFont,
                content: this.nickName
            };
            let descOpt = {
                marginTop: DefaultFontSize * 1.5,
                fontSize: DefaultFontSize * 0.8,
                color: "#888",
                font: DefaultFont,
                content: this.desc
            };

            try {
                this.setupCanvas();
                this.drawBg();
                if (this.avatarFile) {
                    await this.drawImage(avatarOpt);
                }
                if (this.nickName) {
                    this.drawText(nickNameOpt);
                }
                if (this.desc) {
                    this.drawText(descOpt);
                }
                this.$emit("rendered");
            } catch (error) {
                this.$emit("error", error);
            }
        },
        drawBg() {
            // let ctx = this.canvasCtx;
            // ctx.save();
            // ctx.fillStyle = "black";
            // ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);
            // ctx.restore();
        },
        drawText(opt) {
            let ctx = this.canvasCtx;
            ctx.save();
            ctx.font = opt.fontSize + "px " + opt.font;
            ctx.fillStyle = opt.color;

            let centerX = this.canvas.width / 2;
            let centerY = this.canvas.height / 2;
            let x = centerX;
            let y = centerY + opt.marginTop;
            let content = opt.content;

            ctx.textAlign = "center";
            ctx.textBaseline = "middle";
            ctx.fillText(content, x, y);
            ctx.restore();
        },
        async drawImage(opt) {
            let ctx = this.canvasCtx;
            let centerX = this.canvas.width / 2;
            let centerY = this.canvas.height / 2;
            let image = await this.loadImage(opt.file);
            let imageSize = opt.size;
            let imageHeightScaled = 0;
            let imageWidthScaled = 0;
            let x = centerX;
            let y = centerY + opt.marginTop;

            if (image.naturalHeight > image.naturalWidth) {
                imageWidthScaled = imageSize;
                imageHeightScaled =
                    (imageSize / image.naturalWidth) * image.naturalHeight;
            } else {
                imageWidthScaled =
                    (imageSize / image.naturalHeight) * image.naturalWidth;
                imageHeightScaled = imageSize;
            }
            ctx.save();

            ctx.beginPath();
            ctx.fillStyle = "black";
            ctx.arc(x, y, imageSize / 2, 0, Math.PI * 2);
            ctx.closePath();
            ctx.fill();
            //
            ctx.globalCompositeOperation = "source-in";
            ctx.drawImage(
                image,
                x - imageWidthScaled / 2,
                y - imageHeightScaled / 2,
                imageWidthScaled,
                imageHeightScaled
            );

            ctx.restore();
        },
        async loadImage(file) {
            return new Promise(async (o, x) => {
                let url = await CordovaModel.getCdvUrl(file);
                if (location.protocol != "cdvfile:") {
                    url = await UGCResource.assetReadAsDataUrl(url);
                }
                let image = new Image();
                image.onload = o.bind(image, image);
                image.onerror = x.bind(image, "load image fail");
                image.src = url;
            });
        },
        setupCanvas() {
            this.canvas.width = DefaultSize;
            this.canvas.height = DefaultSize;
        },
        getDataPng() {
            return this.$refs.canvas.toDataURL();
        },
        async getBlob() {
            let blob = null;
            blob = await new Promise(o => {
                this.$refs.canvas.toBlob(o);
            });
            return blob;
        }
    },
    mounted() {
        this.canvas = this.$refs.canvas;
        this.canvasCtx = this.$refs.canvas.getContext("2d");
        this.render();
    }
};
</script>