<template>
    <div class="crop-main">
        <PosterMakerTop @close="close" @save="save" />
        <div class="title"></div>
        <div ref="mainBox" class="main-box">
            <VueCropper
                ref="cropper"
                :style="{background: 'transparent'}"
                :img="src"
                :outputSize="1"
                outputType="png"
                :autoCrop="true"
                :fixed="true"
                :fixedNumber="fixedNumber"
                :full="true"
                :original="true"
                :fixedBox="true"
                :canMoveBox="false"
                mode="cover"
            />
        </div>
    </div>
</template>
<style lang="less" scoped>
.title {
    color: white;
    text-align: center;
}

.crop-main {
    position: absolute;
    position: fixed;
    left: 0;
    top: 0;
    background-color: #333;
    width: 100%;
    height: 100%;
    padding-bottom: 20px;
    display: flex;
    flex-direction: column;
}
.main-box {
    flex: 1;
}
</style>
<script>
import { VueCropper } from "vue-cropper";
import PosterMakerTop from "@/views/Top/PosterMakerTop";

export default {
    props: ["src", "ratio"],
    components: {
        PosterMakerTop,
        VueCropper
    },
    computed: {
        fixedNumber() {
            return [this.ratio, 1];
        }
    },
    methods: {
        close() {
            this.$emit("close");
        },
        async save() {
            let cropper = this.$refs.cropper;
            let blob = await new Promise(cropper.getCropBlob.bind(cropper));
            this.$emit("save", blob);
        }
    },
    mounted() {
        // window.console.log("crop src:" + this.src);
    }
};
</script>