<template>
    <div style="width:100%;padding:5px 10px;height:100%;display:flex;flex-direction:column">
        <div style="color:white;padding:5px 0">
            :
            <span
                @click="remove"
                style="background:white;color:gray;border-radius:3px;padding:3px 5px"
            >{{bgm&&bgm.name||""}} {{bgm&&bgm.name&&""}}</span>
        </div>
        <div style="padding:5px 0" class="audio-base-main" @touchend="touchend">
            <span
                style="color:white;width:100px;white-space:nowrap;padding-right:10px"
            >({{volume}})</span>
            <mu-slider style="margin:0" :min="0" :max="300" :step="1" v-model="volume" />
        </div>

        <div style="border-top:1px solid gray;width:100%;display:flex">
            <HScrollBar class="tabs">
                <div class="tab" v-if="isAndroid()" @click="pickAudio"></div>
                <div
                    class="tab"
                    @click="active=idx"
                    :class="{active:active==idx}"
                    v-for="(item,idx) in allBGM.filter(i=>i.list)"
                    :key="idx"
                >{{item.name}}</div>
            </HScrollBar>
        </div>

        <div
            style="height:100%;overflow:auto"
            :key="list.name"
            v-show="active == idx"
            v-for="(list,idx) in allBGM.filter(i=>i.list)"
        >
            <div
                v-for="item in list.list"
                :key="list.name+item.name"
                @click="toggleBGM(item)"
                :class="{active:selectedBGMName == item.name}"
                class="bgmItem"
            >
                <div v-if="item.playing" class="playing musicmu-icon">
                    <mu-icon value="format_align_left" class="icon" size="16"></mu-icon>
                </div>
                <mu-icon v-else class="musicmu-icon" size="14" value="music_note" />
                <span class="musicName">{{ item.name }}</span>
                <span class="musicDuration">{{item.duration}}</span>
                <mu-icon
                    v-if="!item.local_file&&!item.download"
                    class="musicPlaymu-icon"
                    value="arrow_downward"
                />
                <mu-circular-progress
                    v-if="item.download"
                    :value="item.download.percent"
                    :size="60"
                    style="zoom:0.2;margin-left:10px"
                    color="gray"
                    :stroke-width="10"
                />
            </div>
        </div>
    </div>
</template>
<style lang="less" scoped>
.tabs {
    width: 100%;
    white-space: nowrap;
    padding: 5px;
    overflow: auto;
}
.tab {
    display: inline-block;
    color: white;
    padding: 10px 10px;
}
.tab.active {
    border-bottom: 2px solid red;
}
.audio-base-main {
    display: flex;
}

.bgmItem.active {
    background: white;
    border-radius: 5px;
    color: black;
}

.bgmItem {
    color: white;
    display: flex;
    align-items: center;
    font-size: 14px;
    margin: 10px 0;

    .musicmu-icon {
        margin: 0 10px;
    }

    .playing {
        overflow: hidden;
        height: 10px;

        .icon {
            transform: rotate(-90deg);
            animation: playing 0.7s infinite linear;
        }
    }
    .musicPlaymu-icon {
        margin: 0 10px;

        border-radius: 10px;
        background: rgba(255, 0, 0, 0.3);
        font-size: 10px;
    }

    .musicName {
        max-width: 50%;
        display: inline-block;
    }

    .musicDuration {
        font-size: 12px;
        color: gray;
        margin-left: 5px;
        zoom: 0.7;
    }
    @keyframes playing {
        from {
            transform: rotate(-90deg) skewX(0deg) scaleX(1);
        }
        30% {
            transform: rotate(-90deg) skewX(-40deg) scaleX(1.8);
        }
        70% {
            transform: rotate(-90deg) skewX(40deg) scaleX(1);
        }
        to {
            transform: rotate(-90deg) skewX(0deg) scaleX(0.5);
        }
    }
}
</style>
<script>
import HScrollBar from "@/components/HScrollBar";
import { UGCResourceManager as RManager } from "jsugc-websdk";
import { Editlist, Log } from "jsugc-websdk";
import Const from "@/const";
import throttle from "lodash.throttle";
import OpHistory from "@/model/History";
export default {
    components: { HScrollBar },
    data() {
        return {
            active: 0,
            volume: 100,
            allBGM: [],
            bgm: null,
        };
    },
    props: ["accept"],
    computed: {
        selectedBGMName() {
            return this.bgm ? this.bgm.name : "";
        },
    },
    watch: {
        async accept(v) {
            await this.stop();
            // await new Promise(o => {
            //     // 
            //     let download = () => {
            //         if (this.bgm && !this.bgm.local_file) {
            //             setTimeout(download, 200);
            //         } else {
            //             o();
            //         }
            //     };
            //     download();
            // });

            if (v) {
                let oldBgmUrl = Editlist.editlist.bgm.url;

                if (this.bgm) {
                    Editlist.editlist.bgm.url = this.bgm.local_file.toNativeURL();
                    Editlist.editlist.bgm.volume = this.volume;
                } else {
                    Editlist.editlist.bgm.url = "";
                }

                //|| this.volume != this.initState.volume
                if (oldBgmUrl != Editlist.editlist.bgm.url) {
                    Log.uilog("/tool/bgm", `change: ${this.bgm}`);
                    this.$emit("change");

                    if (!Editlist.editlist.bgm.url) {
                        OpHistory.createOperateHistory(
                            Const.Operate.BGMUnset.Type
                        );
                    } else if (!oldBgmUrl) {
                        OpHistory.createOperateHistory(
                            Const.Operate.BGMSet.Type
                        );
                    } else if (this.initState.volume != this.volume) {
                        OpHistory.createOperateHistory(
                            Const.Operate.BGMParamsModify.Type
                        );
                    }
                }

                this.$parent.close();
            }
        },
        volume(v) {
            if (this._audio) {
                this._audio.volume = Math.min(1, v / 100);
            }

            this.changeVolume(v);
        },
    },
    methods: {
        async toggleBGM(bgm) {
            if (bgm.playing && bgm == this.bgm) await this.stop();
            else await this.play(bgm);
        },
        changeVolume: throttle((v) => {
            Editlist.editlist.bgm.volume = v;
        }),
        remove() {
            this.bgm = null;
            this.stop();
        },
        touchend() {
            // this.$emit('change')
        },
        async stop() {
            //
            if (this._audio) {
                this._audio.pause();
                this._audio.removeAttribute("src");
                this._audio.load();
                this._audio.onended && this._audio.onended();
                await new Promise((o) => setTimeout(o, 100));
            }
        },
        async play(music) {
            if (!music.local_file && !music.download) {
                RManager.downloadRes(RManager.ResType.music, music);
                return;
            }

            this.bgm = music;

            if (music.local_file) {
                await this.stop();

                // 
                if (music.nobgm) {
                    this.bgm = null;
                    return;
                }

                music.playing = true;
                this.bgm = null;
                this.bgm = music; //
                await new Promise((o, x) => {
                    this._audio = this._audio || new Audio();
                    this._audio.src = music.local_file.toInternalURL();
                    this._audio.onended = o;
                    this._audio.onerror = x;
                    this._audio.volume = Math.min(1, this.volume / 100);
                    this._audio.play();
                });
                music.playing = false;
                this.bgm = null;
                this.bgm = music; //
                // this.subList = this.subList.slice(0);
            }
        },
        async pickAudio() {
            let result = await RManager.pickAudio();
            if (!result.path) {
                this.$snotify.error("");
                return;
            }
            let music = await this.getLocalAudio("file://" + result.path);
            this.play(music);
        },
        async getLocalAudio(url) {
            let fileEntry = await new Promise(
                resolveLocalFileSystemURL.bind(window, url)
            );
            let music = {
                name: fileEntry.name,
                local_file: fileEntry,
            };
            return music;
        },
    },
    async mounted() {
        this.$emit("update:title", "");

        this.$parent.acceptable();

        this.allBGM = await RManager.fetchResList(RManager.ResType.music);

        this.bgm = this.allBGM
            .flatMap((i) => i.list)
            .filter((i) => {
                return (
                    i.local_file &&
                    i.local_file.toNativeURL() == Editlist.editlist.bgm.url
                );
            })[0];

        if (!this.bgm && Editlist.editlist.bgm.url) {
            this.bgm = await this.getLocalAudio(Editlist.editlist.bgm.url);
        }

        this.volume = Editlist.editlist.bgm.volume;

        this.initState = {
            volume: this.volume,
        };
    },
    beforeDestroy() {
        this.stop();
    },
};
</script>