import axios from "axios";
import md5 from "md5";
import extend from "extend";
import cordovaModel from "./CordovaModel";
import Const from "../const";
import Log from "./Log";

let NOREMOTE = false;

window.clearAllCache = async function() {
    let fs = await new Promise(
        window.requestFileSystem.bind(window, window.TEMPORARY, 0)
    );

    let f = await new Promise(
        fs.root.getDirectory.bind(fs.root, "ugc_res", {})
    );

    return await new Promise(f.removeRecursively.bind(f));
};

let ResourceManager = {
    remoteResRoot:
        location.protocol == "file:"
            ? "http://zb.vgemv.com/app_download/ugc/sdk/resource"
            : "ugc",
    localResRoot: "ugc_res",
    assetResRoot_android: "jsugcsdk_www/dist/ugc",
    assetResRoot_ios: "ugcsdk.bundle/dist/ugc",
    get assetResRoot() {
        if (cordovaModel.isIOS()) {
            return cordova.file.applicationDirectory + this.assetResRoot_ios;
        } else {
            return (
                cordova.file.applicationDirectory + this.assetResRoot_android
            );
        }
    },
    ResType: {
        image: "image",
        music: "music",
        audio: "audio",
        filter: "filter",
        font: "font",
        fontbg: "fontbg",
        mosic: "mosic",
    },
    get remoteResRootJson() {
        return this.remoteResRoot + "/res.json";
    },
    get assetResRootJson() {
        return this.assetResRoot + "/res.json";
    },
    tempFs: null,
    folders: {},
    async filesystem() {
        if (!this.tempFs) {
            this.tempFs = await new Promise(
                window.requestFileSystem.bind(
                    window,
                    window.TEMPORARY,
                    5 * 1024 * 1024
                )
            );
        }
        return this.tempFs;
    },
    remoteRel2Abs(rel1, rel2) {
        return (
            this.remoteResRoot +
            (rel1 ? "/" + rel1 : "") +
            (rel2 ? "/" + rel2 : "")
        );
    },
    localRel2Abs(rel1, rel2) {
        return (
            this.localResRoot +
            (rel1 ? "/" + rel1 : "") +
            (rel2 ? "/" + rel2 : "")
        );
    },
    assetRel2Abs(rel1, rel2) {
        return (
            this.assetResRoot +
            (rel1 ? "/" + rel1 : "") +
            (rel2 ? "/" + rel2 : "")
        );
    },
    async assetReadText(url) {
        let fEntry = await new Promise(
            window.resolveLocalFileSystemURL.bind(window, url)
        );
        let file = await new Promise(fEntry.file.bind(fEntry));
        let reader = new FileReader();
        let result = (
            await new Promise((o, x) => {
                reader.onload = o;
                reader.onerror = x;
                reader.readAsText(file);
            })
        ).target.result;

        // Log.verbose("Asset text:",url, result);
        return result;
    },
    async assetReadAsDataUrl(url) {
        let fEntry = await new Promise(
            window.resolveLocalFileSystemURL.bind(window, url)
        );
        let file = await new Promise(fEntry.file.bind(fEntry));
        let reader = new FileReader();
        let result = (
            await new Promise((o, x) => {
                reader.onload = o;
                reader.onerror = x;
                reader.readAsDataURL(file);
            })
        ).target.result;

        // Log.verbose("Asset text:",url, result);
        return result;
    },
    md5source(source) {
        return (
            md5(
                source
                    .split(".")
                    .slice(0, -1)
                    .join(".")
            ) +
            "." +
            source.split(".").reverse()[0]
        );
    },
    async getFolder(resType, folder) {
        let fs = await this.filesystem();
        let result;

        folder = this.localRel2Abs(resType, folder);

        if (this.folders[folder]) {
            return this.folders[folder];
        }

        //
        let splited = folder.split("/");
        for (let i = 0; i <= splited.length; i++) {
            let currentFolder = splited.slice(0, i).join("/");
            result = await new Promise(
                fs.root.getDirectory.bind(fs.root, currentFolder, {
                    create: true,
                })
            );
            this.folders[currentFolder] = result;
        }
        return result;
    },
    async getTempFolder() {
        let fs = await this.filesystem();
        let folder = this.folders["ugc_temp"];
        if (!folder) {
            folder = await new Promise(
                fs.root.getDirectory.bind(fs.root, "ugc_temp", { create: true })
            );
        }
        return folder;
    },
    async getExportPosterFolder() {
        let fs = await this.filesystem();
        let folder = this.folders["ugc_poster"];
        if (!folder) {
            folder = await new Promise(
                fs.root.getDirectory.bind(fs.root, "ugc_poster", {
                    create: true,
                })
            );
        }
        return folder;
    },
    async clearTempFiles(wait) {
        let fs = await this.filesystem();
        let temp = await this.getTempFolder();
        let thumbs = await new Promise(
            fs.root.getDirectory.bind(fs.root, "thumbs/", { create: true })
        );
        let all = [
            new Promise(temp.removeRecursively.bind(temp)),
            new Promise(thumbs.removeRecursively.bind(thumbs)),
        ];
        if (wait) {
            await Promise.all(all);
        }
    },
    async fetchAssetResList(resType) {
        let resIdx = JSON.parse(
            await this.assetReadText(this.assetResRootJson)
        );
        let resList = JSON.parse(
            await this.assetReadText(this.assetRel2Abs(resIdx[resType]))
        );

        //
        resList = resList.map((i) => {
            if (i.list) {
                i.list = i.list.map((j) => {
                    if (j.source) {
                        j.source_asset_url = this.assetRel2Abs(
                            resType,
                            j.source
                        );
                    }
                    if (j.source_list) {
                        j.source_list_asset_url = j.source_list.map((i) =>
                            this.assetRel2Abs(resType, i)
                        );
                    }
                    return j;
                });
            } else if (i.source) {
                i.source_asset_url = this.assetRel2Abs(resType, i.source);
            }

            return i;
        });
        return resList;
    },
    //
    async fetchLocalResList(resType) {
        if (!window.requestFileSystem) {
            return [];
        }

        let folder = await this.getFolder(resType);

        let reader = folder.createReader();

        let entries = await new Promise(reader.readEntries.bind(reader));

        //
        let files = await Promise.all(
            entries.map(async (d) => {
                if (d.isDirectory) {
                    let reader = d.createReader();
                    let entries = await new Promise(
                        reader.readEntries.bind(reader)
                    );
                    return {
                        ...d,
                        list: entries,
                    };
                } else return d;
            })
        );

        return files;
    },
    //
    async fetchRemoteResList(resType) {
        let resIdx = (await axios.get(this.remoteResRootJson)).data;
        let resList = (await axios.get(this.remoteRel2Abs(resIdx[resType])))
            .data;

        //
        resList = resList.map((i) => {
            if (i.list) {
                i.list = i.list.map((j) => {
                    if (j.source) {
                        j.source_remote_url = this.remoteRel2Abs(
                            resType,
                            j.source
                        );
                        j.download = null;
                    }
                    if (j.source_list) {
                        j.source_list_remote_url = j.source_list.map((i) =>
                            this.remoteRel2Abs(resType, i)
                        );
                        j.download = null;
                    }
                    return j;
                });
            } else if (i.source) {
                i.source_remote_url = this.remoteRel2Abs(resType, i.source);
                i.download = null;
            }

            return i;
        });
        return resList;
    },
    copyedResTypes: {},
    //
    async fetchResList(resType) {
        let local = await this.fetchLocalResList(resType);
        let asset;
        try {
            asset = await this.fetchAssetResList(resType);
        } catch (e) {
            Log.warn("asset resource not found");
            /* */
        }

        let remote;
        try {
            remote = NOREMOTE ? asset : await this.fetchRemoteResList(resType);
        } catch (e) {
            remote = asset;
            Log.warn("remote resource not found");
        }

        Log.verbose("remote", remote);
        Log.verbose("local", local);
        Log.verbose("asset", asset);

        if (!remote) {
            return;
        }

        let needCopyAsset = [];
        let processItem = (directoryName, i) => {
            // 
            let filename = this.md5source(i.source);
            let name = i.name;
            i.local_file = local.filter((j) => j.name == filename)[0];

            i.thumb_remote_url = this.remoteRel2Abs(resType, i.thumb);
            i.thumb_rel_url = "ugc/" + resType + "/" + (i.thumb || "");

            if (i.source_list) {
                i.local_file_list = i.source_list.map((i) => {
                    let filename = this.md5source(i);
                    return local.filter((j) => j.name == filename)[0];
                });
            }

            //assetasset
            if (!i.local_file && asset && !this.copyedResTypes[resType]) {
                let assetList = asset.filter(
                    (i) => i.name == directoryName && i.list
                )[0];
                let assetFound =
                    (assetList &&
                        assetList.list.filter((i) => i.name == name)[0]) ||
                    asset.filter((i) => i.name == name)[0];

                if (assetFound) {
                    needCopyAsset.push(
                        this.copyAssetRes(resType, i, assetFound)
                    );
                }

                //TODO:
                //  asset 
            }

            return i;
        };

        //
        //
        let result = remote.map((i) => {
            //
            if (i.list) {
                let directoryName = i.name;
                i.list = i.list.flatMap(processItem.bind(this, directoryName));
            } else {
                processItem(null, i);
            }
            return i;
        });

        // copy asset 
        //Promise.allSettled  needCopyAsset  Promise.all 
        // await Promise.all(needCopyAsset);// download
        Promise.all(needCopyAsset).then(() => {
            this.copyedResTypes[resType] = true;
        });

        return result;
    },
    async createResFile(resType, sourceUrl) {
        let fs = await this.filesystem();

        //
        await this.getFolder(resType);
        let filename = this.md5source(sourceUrl);

        // Log.verbose("createResFile: folder:",folder)
        let absUrl = this.localRel2Abs(resType, filename);
        Log.verbose("createResFile:", absUrl);
        return await new Promise(
            fs.root.getFile.bind(fs.root, absUrl, { create: true })
        );
    },
    async downloadRes(resType, resItem, progress) {
        let sourceRemoteUrl = resItem.source_remote_url;

        resItem.download = {
            percent: 0,
        };

        let allProgress = [0];
        let totalItem =
            1 + ((resItem.source_list && resItem.source_list.length) || 0);

        let fEntry = await this.createResFile(resType, resItem.source);

        try {
            await this.download(sourceRemoteUrl, fEntry, (p) => {
                resItem.download.percent = p;
                allProgress[0] = p;
                progress &&
                    progress(allProgress.reduce((a, b) => a + b) / totalItem);
            });

            if (resItem.source_list) {
                let local_file_list = [];
                await Promise.all(
                    resItem.source_list_remote_url.map(async (i, idx) => {
                        let fEntry = await this.createResFile(
                            resType,
                            resItem.source_list[idx]
                        );
                        try {
                            await this.download(
                                i,
                                fEntry,
                                (p) =>
                                    (allProgress[idx + 1] =
                                        p &&
                                        progress &&
                                        progress(
                                            allProgress.reduce(
                                                (a, b) => a + b
                                            ) / totalItem
                                        ))
                            );
                            local_file_list.push(fEntry);
                        } catch (e) {
                            fEntry.remove();
                            Log.warn(`download file failed:${i}`);
                        }
                    })
                );

                resItem.local_file_list = local_file_list;
            }

            delete resItem.download;

            // res.source  res.local_file 
            resItem.local_file = fEntry;

            return fEntry;
        } catch (e) {
            fEntry.remove();
            resItem.download = null; // deletevue

            Log.warn(`download file failed:${sourceRemoteUrl}`);
            throw e;
        }
    },

    async download(url, fEntry, progress) {
        Log.info(`begin download:${url}`);

        let data = await new Promise((o, x) => {
            var xhr = new XMLHttpRequest();
            xhr.open("GET", url, true);
            xhr.responseType = "blob";
            xhr.onprogress = async (e) => {
                // Log.verbose("download",url,e.loaded)
                progress && progress(e.loaded / e.total);
            };
            xhr.onerror = x;
            xhr.onload = function() {
                if (this.status == 200) {
                    o(this.response);
                    // var blob = new Blob([this.response], { type: 'image/png' });
                    // saveFile(dirEntry, blob, "downloadedImage.png");
                } else {
                    x(this.status);
                }
            };
            xhr.send();
        });

        let writer = await new Promise(fEntry.createWriter.bind(fEntry));

        await new Promise((o, x) => {
            writer.onwriteend = o;
            writer.onerror = x;
            writer.write(data);
        });
    },
    copingAssetRes: {},
    async copyAssetRes(resType, resItem, assetResItem) {
        let key = resType + "_" + resItem.source + assetResItem.source;
        let loader = this.copingAssetRes[key];
        if (loader) {
            return loader;
        }

        try {
            loader = this.copingAssetRes[key] = this.copyAssetResIntenal(
                resType,
                resItem,
                assetResItem
            );
            await loader;
            delete this.copingAssetRes[key];
            return loader;
        } catch (e) {
            delete this.copingAssetRes[key];
            Log.warn(`copy asset failed:${key}`);
            throw e;
        }
    },
    async copyAssetResIntenal(resType, resItem, assetResItem) {
        // 
        resItem.download = {
            percent: 0,
        };

        // 
        await new Promise((o) => setTimeout(o, 100));

        let fEntry = await this.createResFile(resType, resItem.source);
        // let dEntry = await new Promise(fEntry.getParent.bind(fEntry));
        // getFolder, 
        let dEntry = await this.getFolder(resType);

        try {
            await new Promise(fEntry.remove.bind(fEntry));
        } catch (e) {
            //
        }

        let file;
        try {
            file = await new Promise(
                resolveLocalFileSystemURL.bind(
                    null,
                    assetResItem.source_asset_url
                )
            );
        } catch (e) {
            resItem.download = null;
            Log.warn(assetResItem, "not found");
            return;
        }

        try {
            //glsl lut3d
            fEntry = await new Promise(
                file.copyTo.bind(file, dEntry, fEntry.name)
            );
        } catch (e) {
            ///
        }

        resItem.local_file = fEntry;
        resItem.download = null;

        if (assetResItem.source_list_asset_url) {
            resItem.local_file_list = await Promise.all(
                assetResItem.source_list_asset_url.map(async (i, idx) => {
                    let fEntry = await this.createResFile(
                        resType,
                        resItem.source_list[idx]
                    );
                    let dEntry = await new Promise(
                        fEntry.getParent.bind(fEntry)
                    );

                    try {
                        await new Promise(fEntry.remove.bind(fEntry));
                    } catch (e) {
                        //
                    }

                    let file;
                    try {
                        file = await new Promise(
                            resolveLocalFileSystemURL.bind(null, i)
                        );
                    } catch (e) {
                        Log.verbose(assetResItem, "not found");
                        return;
                    }

                    try {
                        //
                        return await new Promise(
                            file.copyTo.bind(file, dEntry, fEntry.name)
                        );
                    } catch (e) {
                        return fEntry;
                    }
                })
            );
        }
    },

    async getBeautySource() {
        let allFilter = await this.fetchResList(this.ResType.filter);
        let beautyFilter = allFilter.find((i) => i.name == "");
        return beautyFilter.local_file.toNativeURL();
    },
    async getMosicSource() {
        let allFilter = await this.fetchResList(this.ResType.filter);
        let found = allFilter
            .reduce((pre, nxt) => pre.concat(nxt.list || []), [...allFilter])
            .find((i) => i.name == "");
        return found.local_file.toNativeURL();
    },
    async getTransformSource() {
        let allFilter = await this.fetchResList(this.ResType.filter);
        let found = allFilter.find((i) => i.name == "transform");
        return found.local_file.toNativeURL();
    },
    async pickAudio() {
        return await new Promise(UGCApp.pickAudio.bind(UGCApp));
    },
    // 
    _ending: null,
    getLastEnding() {
        try {
            return (
                this._ending || JSON.parse(localStorage.getItem("last-ending"))
            );
        } catch (error) {
            return this._ending;
        }
    },
    saveLastEnding(data) {
        this._ending = data;
        try {
            localStorage.setItem("last-ending", JSON.stringify(data));
        } catch (error) {
            //
        }
    },
};

export default ResourceManager;
