    <div class="dialog-content" style="">
        <mu-button @click="importFile" round large color="red">
            <mu-icon left size="40" value="folder_open" />
            
        </mu-button>
        <mu-button @click="captureFile" round large color="red">
            <mu-icon left size="40" value="folder_open" />
            
        </mu-button>

        <mu-button slot="actions" flat color="primary" @click="close"></mu-button>
    </div>
    </mu-dialog>-->
    <mu-bottom-sheet
        :overlay-close="editlist.timelines.length > 0"
        :esc-press-close="editlist.timelines.length > 0"
        :open.sync="isopen"
    >
        <mu-list @item-click="close">
            <mu-sub-header></mu-sub-header>
            <mu-list-item button @click="importFile">
                <mu-list-item-action>
                    <mu-icon value="folder_open" color="orange"></mu-icon>
                </mu-list-item-action>
                <mu-list-item-title></mu-list-item-title>
            </mu-list-item>
            <!--    -->
            <mu-list-item button @click="captureFile">
                <mu-list-item-action>
                    <mu-icon value="photo_camera" color="orange"></mu-icon>
                </mu-list-item-action>
                <mu-list-item-title></mu-list-item-title>
            </mu-list-item>
            <mu-list-item button @click="$emit('cancel')">
                <mu-list-item-action>
                    <mu-icon value="cancel" color="gray"></mu-icon>
                </mu-list-item-action>
                <mu-list-item-title></mu-list-item-title>
            </mu-list-item>
        </mu-list>
    </mu-bottom-sheet>
