<style scoped>
.dialog-content {
    padding: 24px;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: space-evenly;
    flex-flow: column;
    height: 200px;
}
</style>

<script>
import Const from "@/const";
import { Editlist, Log } from "jsugc-websdk";

export default {
    data() {
        return {
            isopen: false,
            editlist: Editlist.editlist,
        };
    },
    methods: {
        open() {
            this.isopen = true;
        },
        close() {
            this.isopen = false;
        },
        async importFile() {
            Log.uilog("/importer", "pick video from library");
            if (this.isIOS()) {
                // ios  picker 
                try {
                    let result = await new Promise(
                        MediaPicker.getMedias.bind(MediaPicker, {
                            selectMode: 102, //101=picker image and video , 100=image , 102=video
                            maxSelectCount: 1, //default 40 (Optional)
                            // 'maxSelectSize': 188743680, //188743680=180M (Optional) only android
                        })
                    );

                    Log.info(`import file `, result);

                    this.$emit("select", result);
                } catch (e) {
                    Log.error("import file failed: ", e);

                    // 
                    if (!Editlist.editlist.timelines.length) {
                        this.open();
                    }
                }
            } else {
                try {
                    let result = await new Promise((o, x) =>
                        navigator.camera.getPicture(o, x, {
                            // destinationType: Camera.DestinationType.FILE_URI,
                            sourceType: Camera.PictureSourceType.PHOTOLIBRARY,
                            allowEdit: true,
                            mediaType: Camera.MediaType.VIDEO,
                        })
                    );

                    Log.info(`import file `, result);
                    this.$emit("select", [
                        {
                            uri: result,
                        },
                    ]);
                } catch (e) {
                    Log.error("import file failed: ", e);
                    // 
                    if (!Editlist.editlist.timelines.length) {
                        this.open();
                    }
                }
            }
        },
        async captureFile() {
            Log.uilog("/importer", "capture video from camera");
            try {
                // CameraFragment  
                // if(this.isAndroid()) {
                //     let result = await new Promise((o, x) =>
                //         UGCApp.captureVideo(o, x, {
                //             duration: Const.CaptureVideo.Duration
                //         }));
                //     window.console.log(result)
                //     if(!result.path) {
                //         this.$mResToast.error("");
                //         return;
                //     }
                //     this.$emit("select", [{ uri: result.path }]);
                // }
                // else
                {
                    let result = await new Promise((o, x) =>
                        navigator.device.capture.captureVideo(o, x, {
                            duration: Const.CaptureVideo.Duration,
                        })
                    );
                    Log.error("capture file", result);
                    this.$emit(
                        "select",
                        result.map((o) => ({
                            uri: o.fullPath,
                        }))
                    );
                }
            } catch (e) {
                Log.error("capture file failed: ", e);
                if (!Editlist.editlist.timelines.length) {
                    this.open();
                }
            }
        },
        importProgress(progress) {
            if (progress && progress.status == "progress") {
                this.$emit("progress", progress.index);
                // window.console.log("import:",progress);
            }
        },
    },
    mounted() {
        if (this.isIOS()) {
            document.addEventListener(
                "MediaPicker.CompressVideoEvent",
                this.importProgress
            );
        }
    },
    beforeDestroy() {
        if (this.isIOS()) {
            document.removeEventListener(
                "MediaPicker.CompressVideoEvent",
                this.importProgress
            );
        }
    },
};
</script>
