<template>
    <transition name="fade" v-on:after-leave="$emit('close')">
        <div v-if="tabid >= 0" class="dialog-main" :style="style">
            <div class="dialog-content">
                <AudioBase
                    :title.sync="title"
                    ref="audiobBase"
                    @change="change"
                    v-if="tabid == TabID.AudioBase"
                />
                <Sort
                    :title.sync="title"
                    ref="sort"
                    :accept="dialogAccept"
                    @change="change"
                    v-if="tabid == TabID.VideoSort"
                />
                <BGM
                    :title.sync="title"
                    ref="bgm"
                    :accept="dialogAccept"
                    @change="change"
                    v-if="tabid == TabID.BGM"
                />
                <AudioEffect
                    :title.sync="title"
                    ref="audioEffect"
                    :accept="dialogAccept"
                    @change="change"
                    v-if="tabid == TabID.AudioEffect"
                />
                <VFilter
                    :title.sync="title"
                    ref="vfilter"
                    @change="change"
                    v-if="tabid == TabID.Filter"
                />
                <Face
                    :title.sync="title"
                    ref="face"
                    @change="change"
                    v-if="tabid == TabID.Face"
                />
                <ImageOverlay
                    :title.sync="title"
                    ref="imageOverlay"
                    :options="options"
                    :postermode="postermode"
                    @change="change"
                    @beforechange="beforechange"
                    v-if="tabid == TabID.AddImage"
                />
                <TextOverlay
                    :title.sync="title"
                    ref="textOverlay"
                    :postermode="postermode"
                    :accept="dialogAccept"
                    :options="options"
                    @change="change"
                    @beforechange="beforechange"
                    v-if="tabid == TabID.AddText"
                />
                <VideoBase
                    :title.sync="title"
                    ref="videoBase"
                    @change="change"
                    v-if="tabid == TabID.VideoBase"
                />
                <AspectRatio
                    :title.sync="title"
                    ref="aspectRatio"
                    @change="change"
                    v-if="tabid == TabID.AspectRatio"
                />
            </div>
            <div class="bottom-title">
                <mu-button
                    v-if="!dialogAcceptable"
                    class="accept-btn"
                    flat
                    color="white"
                    @click="close"
                >
                    <mu-icon value></mu-icon>
                </mu-button>
                <mu-button
                    v-if="dialogAcceptable"
                    class="close-btn"
                    flat
                    color="white"
                    @click="close"
                >
                    <mu-icon value="close"></mu-icon>
                </mu-button>
                <div class="title">{{title}}</div>
                <mu-button
                    v-if="dialogAcceptable"
                    class="accept-btn"
                    flat
                    color="white"
                    @click="accept"
                >
                    <mu-icon value="check"></mu-icon>
                </mu-button>
                <mu-button
                    v-if="!dialogAcceptable"
                    class="accept-btn"
                    flat
                    color="white"
                    @click="close"
                >
                    <mu-icon value="keyboard_arrow_down"></mu-icon>
                </mu-button>
            </div>
        </div>
    </transition>
</template>
<style lang="less" scoped>
.fade-enter-active {
    transition: all 0.5s;
}
.fade-leave-active {
    transition: all 0.2s;
}
.fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
    transform: translateY(10px);
}
.fade-enter /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
    transform: translateY(50px);
}

.dialog-main {
    width: 100%;
    // position: absolute;
    z-index: 100;
    background: #323232;
    bottom: 0;
    left: 0;
    display: flex;
    flex-flow: column;
}
.bottom-title {
    display: flex;
    height: 36px;
    .title {
        width: 100%;
        color: white;
        display: flex;
        align-items: center;
        justify-content: center;
        border-top: 1px solid;
        border-image: linear-gradient(
                90deg,
                transparent,
                rgba(255, 0, 0, 0.5),
                transparent
            )
            1 10;
    }
    .close-btn {
        width: 50px;
    }
    .accept-btn {
        width: 50px;
    }
}
.dialog-content {
    height: 100%;
    overflow: hidden;
    display: flex;
    padding: 20px;
    padding-bottom: 0;
}

.dialog-content > div {
    width: 100%;
}
</style>
<script>
import AudioBase from "@/views/Bottom/Tools/AudioBase";
import BGM from "@/views/Bottom/Tools/BGM";
import AudioEffect from "@/views/Bottom/Tools/AudioEffect";
import VFilter from "@/views/Bottom/Tools/Filter";
import ImageOverlay from "@/views/Bottom/Tools/ImageOverlay";
import TextOverlay from "@/views/Bottom/Tools/TextOverlay";
import VideoBase from "@/views/Bottom/Tools/VideoBase";
import Sort from "@/views/Bottom/Tools/Sort";
import Face from "@/views/Bottom/Tools/Face";
import AspectRatio from "@/views/Bottom/Tools/AspectRatio";
import ToolTab from "@/views/Bottom/ToolTab";

export default {
    components: {
        AudioBase,
        BGM,
        AudioEffect,
        VFilter,
        ImageOverlay,
        TextOverlay,
        VideoBase,
        Sort,
        Face,
        AspectRatio
    },
    props: ["postermode"],
    data() {
        return {
            tabid: -1,
            title: "",
            dialogAccept: false,
            dialogAcceptable: false,
            TabID: ToolTab.TabID,
            options: null
        };
    },
    computed: {
        style() {
            let ret = {
                height: "auto"
            };
            if (this.options && this.options.height) {
                ret.position = "absolute";
                ret.height = this.options.height;
            }

            return ret;
        }
    },
    methods: {
        show(tabID, options = null) {
            this.title = "";
            this.dialogAccept = false;
            this.dialogAcceptable = false;
            this.tabid = tabID;
            this.options = { ...options };
        },
        beforechange(e) {
            this.$emit("beforechange", e);
        },
        change(e) {
            this.$emit("change", e, this.tabid);
        },
        acceptable() {
            this.dialogAcceptable = true;
        },
        close() {
            this.dialogAccept = false;
            this.triggerClose();
            this.tabid = -1;
        },
        accept() {
            this.dialogAccept = true;

            // dialogAccept 
            // this.$nextTick(
            //     this.$nextTick.bind(this,this.close))
            // this.close();
        },
        triggerClose() {
            Object.keys(this.$refs).forEach(r => {
                this.$refs[r] &&
                    this.$refs[r].onDialogClose &&
                    this.$refs[r].onDialogClose();
            });
        }
    }
};
</script>