<template>
    <div class="audioEffect">
        <mu-list class="main" @change="mainActive=$event" :value="mainActive">
            <mu-list-item
                v-for="item in allAudio"
                :key="item.name"
                :value="item.name"
                :class="{active: mainActive==item.name}"
                :button="true"
            >
                <mu-list-item-title>{{item.name}}</mu-list-item-title>
            </mu-list-item>
        </mu-list>
        <mu-list class="sub">
            <mu-list-item
                @click="chooseSubItem(item)"
                v-for="item in subList"
                :key="item.name"
                :button="true"
            >
                <mu-list-item-title
                    class="subItem"
                    :class="{active: subActive && subActive.name==item.name}"
                >
                    <div v-if="item.playing" class="playing">
                        <mu-icon value="format_align_left" class="icon" size="16"></mu-icon>
                    </div>
                    <mu-icon v-else value="music_note" size="16"></mu-icon>
                    <div class="text">{{item.name}}</div>
                    <mu-icon
                        v-if="!item.local_file&&!item.download"
                        class="musicPlaymu-icon"
                        value="arrow_downward"
                    />
                    <div style="text-align:right">
                        <mu-icon v-if="item.download" value="rotate_left" class="loading" size="16"></mu-icon>
                        <span v-else>{{item.duration}}</span>
                    </div>
                </mu-list-item-title>
            </mu-list-item>
        </mu-list>
    </div>
</template>
<style lang="less" scoped>
.audioEffect {
    width: 100%;
    display: flex;
    flex-direction: row;
    background-color: #333;
    font-size: 12px;

    .main {
        width: 100px;
        text-align: center;

        .active {
            border-right: 2px solid red;
            background: rgba(255, 255, 255, 0.1);
        }
    }

    .sub {
        .subItem {
            font-size: 12px;
            color: white;
            display: flex;
            flex-direction: row;
            align-items: center;

            &.active {
                color: red;
            }

            .text {
                flex: 1;
                margin: 0 10px;
                text-overflow: ellipsis;
                white-space: nowrap;
            }

            .musicPlaymu-icon {
                margin: 0 10px;

                border-radius: 10px;
                background: rgba(255, 0, 0, 0.3);
                font-size: 10px;
            }
            .playing {
                overflow: hidden;
                height: 10px;

                .icon {
                    transform: rotate(-90deg);
                    animation: playing 0.7s infinite linear;
                }
            }

            .loading {
                animation: loading 0.5s infinite linear;
            }
        }
    }
}
@keyframes loading {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(-360deg);
    }
}
@keyframes playing {
    from {
        transform: rotate(-90deg) skewX(0deg) scaleX(1);
    }
    30% {
        transform: rotate(-90deg) skewX(-40deg) scaleX(1.8);
    }
    70% {
        transform: rotate(-90deg) skewX(40deg) scaleX(1);
    }
    to {
        transform: rotate(-90deg) skewX(0deg) scaleX(0.5);
    }
}
</style>
<style>
.audioEffect .mu-item-sub-title,
.audioEffect .mu-item-title {
    color: #fff;
    font-size: 12px;
}
.audioEffect .mu-list {
    padding: 0;
}
.audioEffect .mu-item-action {
    width: 30px;
    min-width: 30px;
}
</style>
<script>
import { UGCResourceManager as RManager } from "jsugc-websdk";
import { Editlist, Log } from "jsugc-websdk";
import Const from "@/const";
import OpHistory from "@/model/History";
export default {
    props: ["accept"],
    data() {
        return {
            mainActive: 1,
            subActive: null,
            allAudio: [],
            subList: null,
        };
    },
    watch: {
        mainActive(v) {
            let found = this.allAudio.filter((i) => i.name == v)[0];
            if (found) this.subList = found.list;
        },
        async accept(v) {
            await this.stop();

            if (v && this.subActive) {
                Editlist.editlist.add_amix(this.subActive);
                Log.uilog(
                    "/tool/audio_effect",
                    `change: ${JSON.stringify(this.subActive)}`
                );
                this.$emit("change");
                OpHistory.createOperateHistory(
                    Const.Operate.AudioEffectAdd.Type
                );
            }

            this.$parent.close();
        },
    },
    methods: {
        async chooseSubItem(music) {
            if (this.subActive == music && music.playing) {
                await this.stop();
                return;
            }

            await this.play(music);
        },
        async stop() {
            //
            if (this._audio) {
                this._audio.pause();
                this._audio.removeAttribute("src");
                this._audio.load();
                this._audio.onended && this._audio.onended();
                await new Promise((o) => setTimeout(o, 100));
            }
        },
        async play(music) {
            if (!music.local_file && !music.download) {
                RManager.downloadRes(RManager.ResType.audio, music);
                return;
            }

            this.subActive = music;

            if (music.local_file) {
                await this.stop();

                music.playing = true;
                this.subList = this.subList.slice(0); //
                await new Promise((o, x) => {
                    this._audio = this._audio || new Audio();
                    this._audio.src = music.local_file.toInternalURL();
                    this._audio.onended = o;
                    this._audio.onerror = x;
                    this._audio.play();
                });
                music.playing = false;
                this.subList = this.subList.slice(0);
            }
        },
    },
    async mounted() {
        this.$emit("update:title", "");
        this.$parent.acceptable();

        this.allAudio = await RManager.fetchResList(RManager.ResType.audio);
        this.mainActive = this.allAudio[0].name;
    },
    beforeDestroy() {
        this.stop();
    },
};
</script>