import PreviewPlayer from "./components/PreviewPlayer";
import Export from "./components/Export";
import CordovaModel from "./model/CordovaModel";
import Editlist from "./model/Editlist";
import UGCResourceManager from "./model/UGCResourceManager";
import Log from "./model/Log";
import Const from "./const";

const install = function(Vue) {
    Vue.component("PreviewPlayer", PreviewPlayer);
    Vue.component("Export", Export);
};

/* istanbul ignore if */
if (typeof window !== "undefined" && window.Vue) {
    install(window.Vue);
}

export {
    Const,
    Log,
    PreviewPlayer,
    Export,
    CordovaModel,
    Editlist,
    UGCResourceManager,
};

export default {
    version: "1.1.2",
    install,
    Const,
    Log,
    PreviewPlayer,
    Export,
    CordovaModel,
    Editlist,
    UGCResourceManager,
};
