let i;
const Const = {
    Overlay: {
        Type: {
            // bit
            Image: 1 << (i = 1),
            Text: 1 << ++i,
            Mosic: 1 << ++i,
        },
        Align: {
            Left: (i = 0),
            Center: ++i,
            Right: ++i,
            Top: ++i,
        },
    },
};

export default Const;
