<template>
    <div
        class="tool-box"
        ref="main"
        :class="{overlay:show_overlay||show_amix}"
        @touchstart="currentStart"
        @touchmove="currentMove"
        @touchend="changed()"
    >
        <div class="main-line going" ref="mainLine" :style="mainLineStyle">
            <div v-if="!postermode" @click="$emit('on_poster_click')" class="video-post-btn">
                <template v-if="poster_url">
                    <img :src="poster_url" @error="posterLoadError" :style="posterStyles" />
                    <span class="text"></span>
                </template>
                <template v-else>
                    <!-- <span class="material-icons">insert_photo</span> -->
                    <span class="text add">+</span>
                </template>
            </div>
            <div class="main-timeline" style>
                <!--  -->
                <!-- ,  -->
                <!--  slot slot  TimelineItem  -->
                <TimelineItem
                    v-for="(i) in timelines"
                    :key="i.id"
                    @change="timelineChange(i.id,$event)"
                    @active="timelineActive(i.id,$event)"
                    @click.native="i.ending? endingClick($event):''"
                    :pps="pps"
                    :dragging="i.ending?true:dragging"
                    :focus="postermode?false:i.focus"
                    :active="(i.ending||postermode)?false:i.active"
                    :timeline="i"
                    :canload_preview="preview_render"
                    :min_sec="video_min_sec"
                ></TimelineItem>
            </div>

            <div v-if="!postermode && !hasEnding" @click="endingClick" class="video-ending-btn">
                <span>+</span>
            </div>

            <div v-if="!postermode && show_overlay" class="sub-timeline" style="margin-top:35px;">
                <template v-for="(i) in overlay">
                    <TimelineOverlayItem
                        v-if="i.type & overlay_type"
                        :key="i.id"
                        @change="overlayChange(i.id,$event)"
                        @active="overlayActive(i.id,$event)"
                        :id="i.id"
                        :type="i.type"
                        :active="i.active"
                        :pps="pps"
                        :end_sec="i.end_sec"
                        :offset_sec="i.offset_sec"
                    ></TimelineOverlayItem>
                </template>
            </div>
            <div v-if="!postermode && show_amix" class="sub-timeline" style="margin-top:35px;">
                <TimelineOverlayItem
                    v-for="(i) in amix"
                    :key="i.id"
                    @change="amixChange(i.id,$event)"
                    @active="amixActive(i.id,$event)"
                    :id="i.id"
                    :is_amix="true"
                    :active="i.active"
                    :pps="pps"
                    :end_sec="i.end_sec"
                    :offset_sec="i.offset_sec"
                ></TimelineOverlayItem>
            </div>
        </div>

        <div class="time-tick" ref="current">
            <div class="current-time" :class="{dragging:dragging}">
                <!-- <mu-icon v-if="modal.playing" type="play_arrow" />
                <mu-icon v-else type="pause" />-->
                {{currentTimeStr}}
            </div>
        </div>
        <div class="total-time-tick">{{totalTimeStr}}</div>
    </div>
</template>

<style lang="less" scoped>
