<script>
/***
 * position, timelines
 *
 * position, timelines
 *
 * position, timelines
 *
 * timeChange, timelinesChange
 */
import TimelineItem from "./TimelineItem";
import TimelineOverlayItem from "./TimelineOverlayItem";
import { CordovaModel } from "jsugc-websdk";
import { Editlist } from "jsugc-websdk";
import { Log } from "jsugc-websdk";
import Const from "@/const";

let Helper = {
    toTimeStr(sec = 0) {
        let min = sec / 60;
        let hour = min / 60;
        let millisec = Math.floor((sec % 1) * 10);
        return (
            Math.floor(hour) +
            ":" +
            Math.floor(min) +
            ":" +
            Math.floor(sec % 60) +
            "." +
            millisec
        );
    },
};
export default {
    components: {
        TimelineItem,
        TimelineOverlayItem,
    },
    props: [
        "playing",
        "show_overlay",
        "show_amix",
        "postermode",
        "poster",
        "overlay_type",
        "preview_render",
        "player",
    ],
    data() {
        return {
            Const,
            pps: screen.width / 10,
            timelines: Editlist.editlist.timelines,
            amix: Editlist.editlist.amix,
            video_min_sec: Editlist.editlist.video_min_sec,
            time_sec: Editlist.editlist.time_sec,
            mainLineStyle: {
                width: 0,
                transform: "",
                "transition-duration": "0s",
            },
            poster_url: "",
            dragging: false,
            timelineChanging: false,
            timer: null,
        };
    },
    watch: {
        async playing(v) {
            if (v) {
                // s
                let UpdateRate = 0.5;

                this.mainLineStyle["transition-duration"] = UpdateRate + "s";

                let fun = async () => {
                    if (this._moveSpeed) return; //not dragging
                    try {
                        let cur = await this.player.position();

                        if (!(cur >= 0)) return;

                        if (cur > this.total_sec) cur = this.total_sec;

                        //css
                        let x = this.getComputedTransformX(cur);

                        let curPos = cur;
                        let timeRemain = this.total_sec - curPos;
                        let timeRemain2 =
                            (this.total_sec * this.pps + x) / this.pps;

                        this.mainLineStyle["transition-duration"] =
                            Math.max(0, timeRemain) + "s";

                        this.mainLineStyle.transform = `translateX(${x}px)`;
                        this.mainLineStyle.transform =
                            "translateX(-" +
                            (this.total_sec + Math.random() / 1000) * this.pps +
                            "px)";

                        Log.verbose(
                            `left:${this.total_sec * this.pps + x} total:${
                                this.total_sec * this.pps
                            } remain:${timeRemain}/${timeRemain2} speed:${
                                (this.total_sec * this.pps + x) / timeRemain
                            }`
                        );

                        this.time_sec = cur;
                        if (!this.playing) {
                            clearInterval(this._goingTmr);
                            //
                            let x = this.getComputedTransformX(cur);
                            this.mainLineStyle["transition-duration"] = "0s";
                            this.mainLineStyle.transform =
                                "translateX(" + x + "px)";
                        }
                    } catch (e) {
                        Log.warn("timeline animation fail");
                    }
                };

                fun();

                this._goingTmr = setInterval(fun, UpdateRate * 1000);
            } else {
                // {
                clearInterval(this._goingTmr);

                //
                let x = this.getComputedTransformX(this.time_sec);
                this.mainLineStyle["transition-duration"] = "0s";
                this.mainLineStyle.transform = "translateX(" + x + "px)";
                // }

                //
                try {
                    let cur = await this.player.position();
                    if (cur >= 0) {
                        this.time_sec = cur;
                        Editlist.editlist.time_sec = cur;
                    }
                } catch (e) {
                    Log.warn("timeline animation fail");
                }

                //
                if (Math.abs(this.time_sec - this.total_sec) < 1) {
                    this.mainLineStyle["transition-duration"] = "0.1s";

                    this.mainLineStyle.transform =
                        "translateX(-" +
                        (this.time_sec + Math.random() / 1000) * this.pps +
                        "px)";
                    setTimeout(() => {
                        this.mainLineStyle["transition-duration"] = "0s";
                    }, 150);
                } else {
                    // lag
                    // this.mainLineStyle["transition-duration"] = "0s";
                    // this.mainLineStyle.transform = "translateX("+(x)+"px)";
                }

                // clearInterval(this._goingTmr2);
            }
        },
        time_sec(v) {
            this.update();

            Editlist.editlist.time_sec = v;
            this.refreshUI();
        },
        async "poster.url"(url) {
            this.poster_url =
                (await CordovaModel.getCdvUrl(url)) + "?" + Math.random();
        },
        timelines() {
            this.update(true);
        },
    },
    computed: {
        overlay() {
            return [...Editlist.editlist.overlay, ...Editlist.editlist.mosic];
        },
        total_sec() {
            return this.timelines.reduce((pre, nxt) => {
                return pre + nxt.end_sec - nxt.start_sec;
            }, 0);
        },
        currentTimeStr() {
            // console.log("update time:"+this.modal.current)
            return Helper.toTimeStr(this.time_sec).replace("0:0", "0");
        },
        totalTimeStr() {
            return Helper.toTimeStr(this.total_sec)
                .split(".")[0]
                .replace("0:0", "0");
        },
        hasEnding() {
            return this.timelines.find((e) => e.ending);
        },
        posterStyles() {
            if (this.poster.rotate) {
                return this.poster.rotate
                    ? "transform: rotate(" + this.poster.rotate + "deg)"
                    : "";
            }
            return {};
        },
    },
    methods: {
        async updateFrame() {},
        seek(sec) {
            if (this.dragging) return;

            this.time_sec = sec;
            this.update(true);
        },
        // idx 
        time_sec_idx(idx) {
            let found = this.timelines.filter((o, i) => {
                return (
                    i == idx &&
                    o.offset_sec <= this.timeSec &&
                    o.offset_sec + (o.end_sec - o.start_sec) > this.timeSec
                );
            });
            if (found.length) return this.time_sec - found[0].offset_sec;
            else return -1;
        },
        update_time_sec(v) {
            this.time_sec = v;
            this.update(true);
        },
        update(computeTransform) {
            if (this.timelineChanging) {
                return;
            }

            //
            this.mainLineStyle.width = this.total_sec * this.pps + "px";

            //
            if (this._moveSpeed || computeTransform) {
                this.mainLineStyle.transform =
                    "translateX(-" + this.time_sec * this.pps + "px)";
            }

            //
            // this.timelines.reduce((pre, nxt) => {
            //     nxt.offset_sec = pre;
            //     return pre + nxt.end_sec - nxt.start_sec;
            // }, 0);
        },
        overlayActive(id, active) {
            if (active) {
                this.overlay.forEach((v) => {
                    v.active = v.id == id;
                });
                this.$emit(
                    "overlayActive",
                    this.overlay.filter((o) => o.id == id)[0]
                );
            } else {
                this.overlay.filter((o) => o.id == id)[0].active = false;
                this.$emit("overlayActive");
            }
        },
        overlayChange(id, event) {
            let found = this.overlay.filter((o) => o.id == id)[0];
            found.offset_sec = event.offset_sec;
            found.end_sec = event.end_sec;

            if (event.overallDragging) {
                this.time_sec = found.offset_sec;
                this.changed({
                    time_sec: this.time_sec,
                });
                this.update(true);
            }

            // window.console.log("overlayCHange:"+event.end)
            if (event.end)
                this.$emit(
                    "overlayChange",
                    this.overlay.filter((o) => o.id == id)[0]
                );
        },
        amixChange(id, event) {
            let found = this.amix.filter((o) => o.id == id)[0];
            found.offset_sec = event.offset_sec;
            found.end_sec = event.end_sec;
            if (event.end)
                this.$emit(
                    "amixChange",
                    this.amix.filter((o) => o.id == id)[0]
                );
        },
        amixActive(id, active) {
            if (active) {
                this.amix.forEach((v) => {
                    v.active = v.id == id;
                });
                this.$emit(
                    "amixActive",
                    this.amix.filter((o) => o.id == id)[0]
                );
            } else {
                this.amix.filter((o) => o.id == id)[0].active = false;
                this.$emit("amixActive");
            }
        },
        timelineActive(id, active) {
            if (active)
                this.timelines.forEach((v) => {
                    if (v.id == id) {
                        v.active = true;

                        if (this.time_sec < v.offset_sec) {
                            this.time_sec = v.offset_sec + 0.01;
                        } else if (
                            this.time_sec >=
                            v.offset_sec + v.end_sec - v.start_sec
                        ) {
                            // 0.0001time_secvtimeline
                            this.time_sec =
                                v.offset_sec + v.end_sec - v.start_sec - 0.0001;
                        }
                    } else {
                        v.active = false;
                    }
                });
            else this.timelines.find((o) => o.id == id).active = false;
            this.update(true);
        },
        timelineChange(id, event) {
            let found = this.timelines.filter((o) => o.id == id)[0];
            found.start_sec = event.start_sec;
            found.end_sec = event.end_sec;
            this.timelineChanging = !event.end;

            let offset_sec = found.offset_sec;
            if (event.left_drag_offset) {
                // timeline item 
                this.timelines.forEach((v) => {
                    if (v.offset_sec < offset_sec) return;
                    v.left_drag_offset = event.left_drag_offset;
                });
            } else {
                this.timelines.forEach((v) => {
                    v.left_drag_offset = 0;
                });
            }

            // seek
            // if(event.end){
            //seek
            if (event.seek_sec !== undefined) {
                let foundIdx = this.timelines.indexOf(found);

                // event.seek_sec seekseek
                let seek_sec =
                    event.seek_sec +
                    [0, ...this.timelines.slice(0, foundIdx)].reduce(
                        (pre, nxt) => {
                            return pre + nxt.end_sec - nxt.start_sec;
                        }
                    );

                this.time_sec = Math.max(0, seek_sec);
                this.changed({
                    time_sec: this.time_sec,
                });
            }
            Editlist.editlist.update_timelines(this.timelines);
            // }

            if (event.end) {
                this.$emit("timelinesChange", this.timelines);
                this.update(true);
            }

            this.update();
        },
        currentStart(e) {
            this._pageX = e.changedTouches[0].pageX;
            this._value = this.time_sec;
            Log.uilog("/timeline", `seek begin: ${this.time_sec}`);
        },
        currentMove(e) {
            let offset = e.changedTouches[0].pageX - this._pageX;

            this.dragging = true;
            //
            if (this._preOffset) this._moveSpeed = -offset + this._preOffset;
            this._preOffset = offset;

            this.time_sec = Math.max(
                0,
                Math.min(this.total_sec, this._value - offset / this.pps)
            );

            this.changed({
                time_sec: this.time_sec,
            });

            return true;
        },
        async changed(opt) {
            let isEnd = !opt;

            if (this.timer) {
                clearTimeout(this.timer);
                this.timer = null;
            }

            if (isEnd && Math.abs(this._moveSpeed) > 1) {
                let speed = this._moveSpeed * 5;
                speed = Math.max(-60, Math.min(60, speed));
                // let ts = Date.now();
                while (Math.abs(speed) > 1) {
                    let newVal = this.time_sec + speed / this.pps;
                    // let now = Date.now();

                    //
                    speed *= 0.8;
                    // speed *= Math.pow(0.8, (now-ts)/20);

                    // ts = now;
                    this.time_sec = Math.max(
                        0,
                        Math.min(this.total_sec, newVal)
                    );

                    await new Promise((o) => {
                        this.timer = setTimeout(o, 20);
                    });
                }
            }

            if (isEnd) {
                this._preOffset = 0;
                this._moveSpeed = 0;
                this.dragging = false;
                Log.uilog("/timeline", `seek end: ${this.time_sec}`);
            }

            this.$emit("timeChange", {
                time_sec: this.time_sec,
                ...opt,
            });
        },
        posterLoadError() {
            this.poster_url = null;
        },
        endingClick() {
            this.$emit("on_ending_click");
        },
        refreshUI() {
            if (this.timelineChanging) {
                return;
            }
            let cur = Editlist.editlist.cur_timeline();
            if (cur) {
                cur.focus = true;

                this.timelines.forEach(
                    (e) => e.id != cur.id && (e.focus = false)
                );

                if (!cur.active) {
                    this.timelines.forEach((e) => (e.active = false));
                }
            }
        },
        getComputedTransformX(time) {
            let style = window.getComputedStyle(this.$refs.mainLine);
            let x = Number.parseFloat(style.transform.split(/[(,)]/)[5]);
            // mainLinestyle.transformnone
            if (isNaN(x)) {
                x = -time * this.pps;
            }
            return x;
        },
    },
    mounted() {
        this.update(true);
        this.updateFrame();
    },
    beforeDestroy() {
        clearInterval(this._goingTmr);
    },
};
</script>
