<template>
    <canvas v-if="is_png" ref="canvas" />
    <img ref="img" v-else @load="onImgLoad" @error="$emit('error',$event)" :src="src" />
</template>
<style scoped>
</style>
<script>
import apngjs from "./apngjs/parser";
import { Log } from "jsugc-websdk";
import { Editlist } from "jsugc-websdk";
export default {
    props: ["src", "overlay"],
    data() {
        return {
            player: null,
            is_png: true
        };
    },
    methods: {
        seek(sec) {
            if (sec === undefined) sec = this.overlay.offset_sec;
            sec -= this.overlay.offset_sec;
            (async () => {
                this.player && this.player.seek(sec * 1000);
            })();
        },
        play() {
            this.player && this.player.play();
        },
        pause() {
            this.player && this.player.pause();
        },
        onImgLoad() {
            this.naturalWidth = this.$refs.img.naturalWidth;
            this.naturalHeight = this.$refs.img.naturalHeight;
            this.$emit("load");
        }
    },
    async mounted() {
        Log.verbose(`apng src: ${this.src}`);

        let fEntry = await new Promise(
            window.resolveLocalFileSystemURL.bind(window, this.src)
        );
        let file = await new Promise(fEntry.file.bind(fEntry));

        let reader = new FileReader();
        let result = (
            await new Promise((o, x) => {
                reader.onload = o;
                reader.onerror = x;
                reader.readAsArrayBuffer(file);
            })
        ).target.result;

        const apng = apngjs(result);
        if (apng instanceof Error) {
            this.is_png = false;
            return;
        }

        let canvas = this.$refs.canvas;
        canvas.width = this.naturalWidth = apng.width;
        canvas.height = this.naturalHeight = apng.height;

        this.player = await apng.getPlayer(canvas.getContext("2d"));
        this.seek(Editlist.editlist.time_sec);
        this.$emit("load");
    }
};
</script>